@import "../../scss/bootstrap-4.1.2/scss/functions";
@import "../../scss/variables";
@import "../../scss/mixins/font-style";
@import "../../scss/mixins/story-image";
@import "../../scss/mixins/no-padding-margin-border";

@mixin PlayfairDisplay {
  font-family: 'PlayfairDisplay';
}

.box-style-13 {
  background: #E9ECEF;
  border: 1px solid #CED4DA;
  box-sizing: border-box;
  border-radius: 4px;
  padding: 20px $spacer-3 $spacer-3;
  text-align: center;

  .box-heading {
    a {
      font-style: normal;
      font-weight: 900;
      font-size: 18px;
      line-height: 24px;
      text-align: center;
      text-transform: uppercase;
      color: #343A40;
      @include PlayfairDisplay()
    }

    .time {
      font-style: normal;
      font-weight: normal;
      font-size: 13px;
      line-height: 20px;
      text-align: center;
      color: #212529;
      display: block;
    }
  }

  .box-content {
    width: 265px;
    height: 373px;
    border: 1px solid #DEE2E6;
    margin-top: 12px;
  }
}