@mixin PlayfairDisplay {
    font-family: 'PlayfairDisplay';
}

@mixin border-bottom {
    &:before {
        position: absolute;
        content: "";
        height: 5px;
        width: 45px;
        background: #bd1723;
        bottom: 0;
        left: 0;
        display: block;
    }
}

// > layout
.container {
    width: 1287px !important;
    max-width: 1287px !important;
}

.section-homepage,
.location-detail-page {
    .abf-homepage {

        .box-abf-1 {
            padding-right: $spacer-3;
        }

        .box-abf-2 {
            width: 694px;
            padding: 0 16px;
            order: 1;
        }

        .box-abf-3 {
            padding-left: $spacer-3;
            width: 280px;
            order: 2;

            >* {
                margin-top: 32px;

                &:first-child {
                    margin-top: 0;
                }
            }
        }

        .box-abf-1,
        .box-abf-2 {
            border-right: 1px solid #E9ECEF;
        }
    }

    .zone-1 {
        padding-top: 20px;
        border-top: 8px solid #F8F9FA;
        display: flex;
        margin-top: $spacer-6;
        justify-content: space-between;
        position: relative;

        &::before {
            position: absolute;
            right: 50%;
            width: 1px;
            height: calc(100% - 20px);
            content: "";
            background: $border-color;
        }
    }
}

.zone-2 {
    background: #FFF9E4;
    padding-top: 40px;
    padding-bottom: 60px;
    margin-top: 62px;
}

.zone-3 {
    margin-top: 48px;
    display: flex;

    >.rank-1 {
        width: 247px;
        padding-right: $spacer-3;
        margin-top: -10px;

        >* {
            margin-top: 50px;

            &:first-child {
                margin-top: 0;
            }
        }
    }

    >.rank-2 {
        width: 725px;
        padding: 0 $spacer-3;
        border-right: 1px solid $border-color;
        border-left: 1px solid $border-color;
    }

    >.rank-3 {
        width: 313px;
        padding-left: $spacer-3;

        >* {
            margin-top: 32px;

            &:first-child {
                margin-top: 0;
            }
        }

        div {
            >* {
                margin-top: 32px;

                &:first-child {
                    margin-top: 0;
                }
            }
        }
    }
}


.zone-4 {
    margin-top: 30px;
    padding-top: 30px;
    border-top: 1px solid #CED4DA;
    display: flex;
    flex-wrap: wrap;

    >* {
        margin-right: 33px;
        margin-top: 20px;

        &:nth-child(-n+4) {
            margin-top: 0;
        }

        &:nth-child(4) {
            margin-right: 0;
        }
    }
}

.zone-5 {
    background: #F8F9FA;
    margin-top: 48px;
    padding: $spacer-4 0;
}

.zone-6 {
    margin-top: 48px;

    .zone-heading {
        border-top: 8px solid #f8f9fa;

        >.wrap-heading a {
            font-style: normal;
            text-transform: uppercase;
            font-weight: 900;
            font-size: 22px;
            line-height: 29px;
            color: #343A40;
            @include PlayfairDisplay();
            padding-bottom: 13px;
            position: relative;
            padding-top: 16px;
            display: block;
            @include border-bottom();
            pointer-events: none;
        }
    }

    .zone-content {
        margin-top: 22px;
        display: flex;
        flex-wrap: wrap;
        margin-top: 20px;

        &:first-child {
            margin-top: 0;
        }

        >* {
            margin-top: 20px;
            margin-right: 33px;

            &:nth-child(-n+5) {
                margin-top: 0;
            }

            &:nth-child(5) {
                margin-right: 0;
            }
        }

        .row {
            justify-content: space-between;
            width: 100%;
        }

        .rank-1 {
            display: flex;
            justify-content: space-between;
            flex-wrap: wrap;

            .box-style-20 {
                margin-top: 20px;

                &:nth-child(-n+4) {
                    // margin-top: 0;
                }
            }
        }

        .rank-2 {
            margin-top: 51px;

            >* {
                margin-top: 20px;

                &:first-child {
                    margin-top: 0;
                }

                img {
                    width: 100%;
                }
            }
        }
    }
}

.zone-3 {
    border-top: 8px solid #F8F9FA;
    margin-top: 20px;
    padding-top: 24px;
}

.box-style-26 {
    margin-top: 17px;
    display: flex;

    .rank-1 {
        width: 511px;
        padding-right: $spacer-3;
    }

    .rank-2 {
        width: 527px;
        padding: 0 $spacer-3;
        border-left: 1px solid $border-color;
        border-right: 1px solid $border-color;
        position: relative;

        &::before {
            position: absolute;
            content: "";
            width: 1px;
            height: 100%;
            background-color: $border-color;
            right: 50%;
        }
    }

    .rank-3 {
        width: 247px;
        padding-left: $spacer-3;
    }
}

.section-article {
    .main-content {
        max-width: 1089px;
        margin: 0 auto;
        margin-top: 30px;
    }

    .wrap-col {
        display: flex;
        justify-content: space-between;

        .main-col {
            width: 792px;

            >* {
                margin-top: $spacer-3;

                &:first-child {
                    margin-top: 0;
                }
            }
        }

        .sub-col {
            width: 264px;
            margin-top: -10px;

            >* {
                margin-top: 32px;

                &:first-child {
                    margin-top: 0;
                }
            }
        }
    }
}

.topic-page {
    .main-content {
        margin-top: 20px;
        justify-content: space-between;
        display: flex;
        flex-wrap: wrap;

        >* {
            margin-top: 32px;
            width: calc(50% - 16.5px);

            &:nth-child(-n+2) {
                margin-top: 0;
            }
        }
    }
}

.search-page,
.tags-page {
    .container {
        .row {
            margin-top: 48px;
            justify-content: space-between;
        }
    }
}

.location-list {
    .container {
        .row {
            margin-top: 24px;
            justify-content: space-between;
        }
    }

    .col-10 {
        margin-top: 23px;
    }
}

.tags-page,
.location-list {
    .col-10 {
        border: 1px solid #DEE2E6;
        padding: 32px;
        border-radius: 8px;

        >* {
            margin-top: 32px;

            &:first-child {
                margin-top: 0;
            }
        }
    }
}

.location-detai-page .site-body {
    .container {
        margin-top: 160px;
    }
}

.longform-page,
.emagazine-page,
.photo-page,
.search-page,
.location-detail-page {
    .region-hot-news {
        display: none;
    }
}

.location-detail-page .site-body {
    .container {
        padding-top: 155px;
    }
}

.photo-page {
    .site-body {
        .container {
            margin-top: 48px;
        }
    }
}

.interview-page {
    .notebox {
        background: #F8F9FA !important;
        border: 1px solid #DEE2E6 !important;
    }
}

.podcast-page {
    .breadcrumbs {
        margin-top: 22px;
    }

    .box-comment {
        width: 792px;
        margin: 0 auto;
    }
}

