@import "../../scss/bootstrap-4.1.2/scss/functions";
@import "../../scss/variables";
@import "../../scss/mixins/font-style";
@import "../../scss/mixins/story-image";
@import "../../scss/mixins/no-padding-margin-border";

@mixin PlayfairDisplay {
  font-family: 'PlayfairDisplay';
}

@mixin Roboto {
  font-family: 'Roboto';
}

@mixin NotoSans {
  font-family: 'NotoSans';
}

.box-style-1 {
  margin-top: .8rem;

  .box-heading {
    a {
      font-weight: bold;
      font-size: 18px;
      line-height: 20px;
      text-transform: uppercase;
      color: #BD1723;
      display: block;
      width: 100%;

      i {
        font-weight: 900;
        font-size: 20px;
        line-height: 26px;
        margin-right: .8rem;
        color: #BD1723;
      }
    }
  }

  .box-content {
    margin-top: 12px;
    display: flex;

    .rank-2 {
      width: 313px;
      border-right: 1px solid #E9ECEF;

      .story {
        width: 297px;

        &__heading a {
          font-style: normal;
          font-weight: bold;
          font-size: 20px;
          line-height: 30px;
          color: #212529;
          @include PlayfairDisplay();
        }

        &__sub-heading a {
          padding-top: 16px;
          display: block;
          font-style: normal;
          font-weight: 500;
          font-size: 14px;
          line-height: 22px;
          color: #212529;

          &:hover {
            color: $primary;
          }
        }

        &__time {
          font-style: normal;
          font-weight: normal;
          font-size: 12px;
          line-height: 22px;
          color: #ADB5BD;
          margin-top: .8rem;
        }

        &__summary {
          margin-top: 4px;
          font-style: normal;
          font-weight: normal;
          font-size: 13px;
          line-height: 22px;
          color: #6C757D;
          @include Roboto();
          overflow: hidden;
          text-overflow: ellipsis;
          display: -webkit-box;
          -webkit-line-clamp: 6;
          line-clamp: 6;
          -webkit-box-orient: vertical;
        }
      }
    }

    .rank-1 {
      order: 1;
      padding-left: 16px;
      text-align: center;
      border-right: 0;

      .story {
        width: 330px;

        &__thumb {
          width: 330px;
          height: 235px;
        }

        &__heading a {
          padding-top: .8rem;
          font-style: normal;
          font-weight: bold;
          font-size: 20px;
          line-height: 27px;
          text-align: center;
          color: #212529;
          @include PlayfairDisplay();
        }

        &__time {
          @include NotoSans();
          font-style: normal;
          font-weight: normal;
          font-size: 12px;
          line-height: 22px;
          color: #ADB5BD;
          margin-top: .8rem;
        }
      }
    }
  }
}