@import "../../scss/bootstrap-4.1.2/scss/functions";
@import "../../scss/variables";
@import "../../scss/mixins/font-style";
@import "../../scss/mixins/story-image";
@import "../../scss/mixins/no-padding-margin-border";

@mixin PlayfairDisplay {
  font-family: 'PlayfairDisplay';
}

.box-style-26 {
  .rank-1 {
    .story {
      &__thumb {
        width: 495px;
        height: 353px;
      }

      &__heading a {
        font-style: normal;
        font-weight: bold;
        font-size: 24px;
        line-height: 30px;
        color: #212529;
        @include PlayfairDisplay();
        padding-top: $spacer-4;
      }
    }
  }

  .rank-2 {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;

    .story {
      width: 231px;
      margin-top: $spacer-3;
      padding-top: $spacer-3;
      border-top: 1px solid $border-color;

      &:nth-child(-n+2) {
        margin-top: 0;
        padding-top: 0;
        border-top: 0;
      }

      &__thumb {
        width: 231px;
        height: 160px;
      }

      &__heading a {
        font-style: normal;
        font-weight: 500;
        font-size: 14px;
        line-height: 20px;
        color: #212529;
        padding-top: 14px;
      }
    }
  }

  .rank-3 {
    .story {
      margin-top: $spacer-3;
      padding-top: $spacer-3;
      border-top: 1px solid $border-color;

      &:first-child {
        margin-top: 0;
        padding-top: 0;
        border-top: 0;
      }

      &__heading a {
        font-style: normal;
        font-weight: 500;
        font-size: 14px;
        line-height: 20px;
        color: #212529
      }
    }
  }
}

.section-cate {
  .box-style-26 .story__heading a:hover {
    color: #0073bf !important;
  }
}