
    @import "../../scss/bootstrap-4.1.2/scss/functions";
    @import "../../scss/variables";
    @import "../../scss/mixins/font-style";
    @import "../../scss/mixins/story-image";
    @import "../../scss/mixins/no-padding-margin-border";

    .box-style-3 {

      border-radius: 0px 0px 4px 4px;

      .box-heading a {
        background: url(../img/img-02.png) center center/100% no-repeat;
        height: 80px;
        overflow: hidden;
        font-size: 0;
        width: 100%;
        display: block;
      }

      .box-content {
        padding: 20px;
        background: #F8F9FA;
      }

      .story {
        margin-top: $spacer-3;
        padding-top: 20px;
        border-top: 1px solid rgba(0, 0, 0, 0.05);

        &:first-child {
          margin-top: 0;
          padding-top: 0;
          border-top: 0;
        }

        &__heading a {
          font-style: normal;
          font-weight: 500;
          font-size: 14px;
          line-height: 20px;
          color: #212529
        }
      }
    }