@import "../../scss/bootstrap-4.1.2/scss/functions";
@import "../../scss/variables";
@import "../../scss/mixins/font-style";
@import "../../scss/mixins/story-image";
@import "../../scss/mixins/no-padding-margin-border";

@mixin PlayfairDisplay {
  font-family: 'PlayfairDisplay';
}

@mixin border-bottom {
  &:before {
    position: absolute;
    content: "";
    height: 5px;
    width: 45px;
    background: #bd1723;
    bottom: 0;
    left: 0;
    display: block;
  }
}

.box-style-15 {
  .box-heading {
    a {
      text-transform: uppercase;
      font-style: normal;
      font-weight: 900;
      font-size: 22px;
      line-height: 29px;
      color: #343A40;
      @include PlayfairDisplay();
      position: relative;
      padding-bottom: 13px;
      @include border-bottom();
      display: block;
    }
  }

  .box-content {
    margin-top: 20px;

    .story {
      overflow: auto;
      padding-top: $spacer-3;
      margin-top: $spacer-3;
      border-top: 1px solid $border-color;

      &:first-child {
        border-top: none;
        padding-top: 0;
        margin-top: 0;
      }

      &__thumb {
        width: 80px;
        height: 60px;
        float: left;
        margin-right: $spacer-3;
        position: relative;

        span {
          background: rgba(0, 0, 0, 0.4);
          position: absolute;
          width: 100%;
          height: 100%;
        }

        i {
          font-weight: 900;
          font-size: 18px;
          line-height: 20px;
          color: #FFFFFF;
          position: absolute;
          top: 50%;
          right: 50%;
          transform: translate(50%, -50%);
        }
      }

      &__heading a {
        font-style: normal;
        font-weight: 500;
        font-size: 14px;
        line-height: 22px;
        color: #343A40;
      }
    }
  }
}