@import "../../scss/bootstrap-4.1.2/scss/functions";
@import "../../scss/variables";
@import "../../scss/mixins/font-style";
@import "../../scss/mixins/story-image";
@import "../../scss/mixins/no-padding-margin-border";

.site-footer {
    margin-top: 48px;

    ul {
        display: flex;
        align-items: center;
        overflow-x: scroll;
        overflow-y: hidden;

        li:first-child {
            a {
                margin-left: 0;
            }
        }

        a {
            font-style: normal;
            text-transform: uppercase;
            color: #000000;
            padding-bottom: 10px;
            display: block;
            font-weight: bold;
            font-size: 12px;
            line-height: 20px;
            white-space: nowrap;
            margin-left: 40px;

            &:hover {
                color: $primary;
            }
        }
    }

    .wrap {
        display: flex;
        justify-content: space-between;
        padding-bottom: 24px;
        padding-top: 17px;
        border-top: 8px solid $border-color;

        .logo {
            background: url(../img/logo-ver2.png) center center no-repeat;
            width: 249px;
            height: 93px;
        }

        .info {
            text-align: right;

            >* {
                font-style: normal;
                font-weight: normal;
                font-size: 14px;
                line-height: 24px;
                text-align: right;
                color: #212529;
            }
        }
    }
}