@mixin NotoSans {
    font-family: 'NotoSans';
}

@mixin Roboto {
    font-family: 'Roboto';
}

.box-style-33 {
    margin-top: 33px;
    padding-top: 16px;
    border-top: 8px solid #F0F6FF;

    .row {
        justify-content: space-between;
    }


    .box-heading {
        display: none;

        .heading {
            font-style: normal;
            font-weight: 600;
            font-size: 32px;
            line-height: 44px;
            text-transform: uppercase;
            color: #212529;
            @include NotoSans();
            padding-bottom: 8px;
            display: block;
            position: relative;
            margin-bottom: 10px;

            &::before {
                position: absolute;
                content: '';
                bottom: 0;
                width: 45px;
                height: 5px;
                background: #BD1723;
                left: 0;
            }
        }
    }

    .heading {
        font-style: normal;
        font-weight: 600;
        font-size: 22px;
        line-height: 30px;
        text-transform: uppercase;
        color: #212529;
        @include NotoSans();
        display: block;

        &:hover {
            color: #0073bf !important;
        }
    }

    .list-story {
        margin-top: 12px;

        .story {
            &:first-child {
                .story__heading a {
                    font-size: 18px;
                    line-height: 26px;
                    color: #212529;
                    padding-top: 8px;
                }
            }

            &__thumb {
                width: 231px;
                height: 154px;
                border-radius: 4px;
            }

            &__heading a {
                font-style: normal;
                font-weight: 500;
                font-size: 14px;
                line-height: 20px;
                color: #495057;
                @include Roboto();
                padding-top: 12px;

                &:hover {
                    color: #0073bf !important
                }
            }
        }
    }
}