@mixin NotoSans {
    font-family: 'NotoSans';
}

@mixin Roboto {
    font-family: 'Roboto';
}

.box-style-28 {
    margin-top: 30px;

    .box-heading {
        background: #F8F9FA;
        border-radius: 4px;
        width: 243px;
        height: 44px;
        display: flex;
        align-items: center;

        .heading {
            padding: 10px 10px 10px 57px;
            font-style: normal;
            font-weight: 600;
            font-size: 18px;
            line-height: 25px;
            text-align: right;
            text-transform: uppercase;
            color: #343A40;
            display: block;
            width: 100%;
            text-align: left;
            background: url(../img/icon-thoidam.png) left 10px center/30px no-repeat;
        }
    }

    .box-content {
        margin-top: 11px;

        .story {

            &__heading a {
                font-style: normal;
                font-weight: 500;
                font-size: 16px;
                line-height: 22px;
                @include Roboto();
                color: #212529;
            }

            &__summary {
                overflow: hidden;
                text-overflow: ellipsis;
                display: -webkit-box;
                -webkit-line-clamp: 3;
                line-clamp: 3;
                -webkit-box-orient: vertical;
                margin-top: 2px;
                font-style: normal;
                font-weight: normal;
                font-size: 14px;
                line-height: 22px;
                @include Roboto();
                color: #6C757D;
            }
        }
    }

    .slick-dots {
        display: flex;
        justify-content: center;

        li {
            width: 8px;
            height: 8px;
            margin-left: 8px;

            &:first-child {
                margin-left: 0;
            }
        }

        li.slick-active {
            button {
                background: #495057;
            }
        }

        button {
            background: #E9ECEF;
            width: 8px;
            height: 8px;
            border-radius: 50%;
            font-size: 0;
        }
    }
}