@mixin NotoSans {
    font-family: 'NotoSans';
}

@mixin Roboto {
    font-family: 'Roboto';
}

.box-style-30 {
    margin-top: 52px;

    .heading {
        font-style: normal;
        font-weight: 600;
        font-size: 22px;
        line-height: 30px;
        text-transform: uppercase;
        color: #212529;
        padding-left: 14px;
        @include NotoSans();
        position: relative;

        &:hover {
            color: #0073bf !important;
        }

        &::before {
            position: absolute;
            content: '';
            width: 137px;
            height: 14px;
            background: #FFF3CD;
            left: 0;
            bottom: 0;
            z-index: -1;
        }
    }

    .box-content {
        margin-top: 21px;

        .story {
            overflow: auto;
            margin-top: 24px;

            &:first-child {
                margin-top: 0;
            }

            &__thumb {
                border-radius: 4px;
                width: 110px;
                height: 80px;
                float: left;
                margin-right: 16px;
            }

            &__heading a {
                font-style: normal;
                font-weight: 500;
                font-size: 16px;
                line-height: 24px;
                color: #212529;
                @include Roboto();
            }
        }
    }
}