@import "../../scss/bootstrap-4.1.2/scss/functions";
@import "../../scss/variables";
@import "../../scss/mixins/font-style";
@import "../../scss/mixins/story-image";
@import "../../scss/mixins/no-padding-margin-border";

@mixin PlayfairDisplay {
  font-family: 'PlayfairDisplay';
}

@mixin Roboto {
  font-family: 'Roboto';
}

.box-style-8 {
  .wrap-heading a {
    font-style: normal;
    font-weight: bold;
    font-size: 28px;
    line-height: 36px;
    text-align: center;
    color: #000000;
    @include PlayfairDisplay();
    position: relative;
    display: block;
    padding-bottom: 24px;
    pointer-events: none;

    &:after {
      content: "";
      position: absolute;
      background: #FFC107;
      width: 80px;
      height: 8px;
      bottom: 0;
      right: 50%;
      transform: translateX(50%);
    }
  }

  .des {
    font-style: normal;
    font-weight: 300;
    font-size: 18px;
    line-height: 28px;
    text-align: center;
    color: #6C757D;
    max-width: 600px;
    @include Roboto();
    margin: 0 auto;
    margin-top: $spacer-3;
  }

  .box-content {
    margin-top: $spacer-4;
  }

  .tab-pane {
    display: none;

    &.active {
      display: block;
    }
  }

  .nav-tab {
    &__link {
      display: flex;
      justify-content: center;
      align-items: center;

      li:last-child {
        a {
          border-right: none;
        }
      }

      a {
        font-weight: normal;
        font-size: 13px;
        line-height: 28px;
        text-align: center;
        text-transform: uppercase;
        color: #6C757D;
        padding: 0 26px;
        border-right: 1px solid #CED4DA;
        @include Roboto;

        &:hover,
        &.active {
          color: #000000;
          // font-weight: bold;
          text-shadow: 0 0 .65px #000, 0 0 .65px #000;
        }
      }
    }

    &__content {
      margin-top: $spacer-3;
    }
  }
}