@import "../../scss/bootstrap-4.1.2/scss/functions";
@import "../../scss/variables";
@import "../../scss/mixins/font-style";
@import "../../scss/mixins/story-image";
@import "../../scss/mixins/no-padding-margin-border";

@mixin PlayfairDisplay {
  font-family: 'PlayfairDisplay';
}

@mixin Roboto {
  font-family: 'Roboto';
}

@mixin NotoSans {
  font-family: 'NotoSans';
}

.hightlight {
  .rank-1 {
    text-align: center;

    .story {
      &__thumb {
        width: 660px;
        height: 417px;
      }

      &__heading a {
        font-style: normal;
        font-weight: bold;
        font-size: 28px;
        line-height: 36px;
        color: #212529;
        padding-top: 12px;
        @include PlayfairDisplay();
      }

      &__time {
        font-style: normal;
        font-weight: normal;
        font-size: 12px;
        line-height: 22px;
        color: #ADB5BD;
        margin-top: 4px;
        @include Roboto;
      }

      &__summary {
        margin-top: 4px;
        font-style: normal;
        font-weight: normal;
        font-size: 13px;
        line-height: 22px;
        color: #6C757D;
        @include Roboto()
      }
    }
  }

  .rank-2 {
    padding-top: 24px;
    position: relative;
    padding-bottom: 12px;
    width: 564px;
    margin: 0 auto;
    margin-top: 24px;
    border-right: none;

    &::before {
      position: absolute;
      top: 0;
      content: "";
      background-color: #000000;
      width: 129px;
      height: 1px;
      right: 50%;
      transform: translateX(50%);
    }

    .story {
      margin-top: $spacer-3;

      &:first-child {
        margin-top: 0;
      }

      &__heading a {
        font-style: normal;
        font-weight: 600;
        font-size: 18px;
        line-height: 28px;
        text-align: center;
        color: #212529;
        @include PlayfairDisplay();
      }

      &__time {
        margin-top: 4px;
        font-style: normal;
        font-weight: normal;
        font-size: 12px;
        line-height: 22px;
        text-align: center;
        color: #ADB5BD;
        @include NotoSans()
      }
    }
  }
}