@mixin NotoSans {
    font-family: 'NotoSans';
}

@mixin Roboto {
    font-family: 'Roboto';
}

.header-thoinay {
    .container {
        padding-top: 5px;
        display: flex;
        justify-content: space-between;
        @include NotoSans();
    }

    .logo {
        font-size: 0;
        width: 160px;
        height: 111px;
        display: block;
        background: url(../img/logo-thoinay.png) center center no-repeat;
    }

    // region-2
    .region-2 {
        align-self: flex-end;
    }

    .top {
        display: flex;
        justify-content: space-between;

        ul {
            display: flex;

            a {
                font-style: normal;
                font-weight: 500;
                font-size: 11px;
                line-height: 15px;
                text-align: right;
                text-transform: uppercase;
                color: #052C65;
                padding-left: 12px;
                margin-left: 12px;
                border-left: 1px solid #DEE2E6;

                &:hover,
                &.active {
                    color: #0073BF;
                }

            }

            li:first-child {
                a {
                    margin-left: 0;
                    padding-left: 0;
                    border-left: none;
                }
            }
        }

        .search {
            position: relative;

            i {
                position: absolute;
                right: 0;
                top: 50%;
                transform: translateY(-50%);
                color: #000000;
                font-size: 17px;
                font-weight: 500;
                cursor: pointer;
                padding: 7px 10px;
            }
        }

        input {
            width: 200px;
            height: 33px;
            background: #F8F9FA;
            outline: none;
            border: none;
            padding: 4px 40px 4px 10px;
        }
    }

    .bottom {
        >ul {
            display: flex;
            justify-content: space-between;
            align-items: center;

            a {
                font-style: normal;
                font-weight: 600;
                font-size: 18px;
                line-height: 25px;
                text-align: right;
                text-transform: uppercase;
                color: #343A40;
                padding: 8px;
                margin-left: 8px;
                display: block;

                &:hover,
                &.active {
                    color: #0073BF !important;
                }
            }

            >li:first-child {
                padding: 8px 8px 8px 0;

                a {
                    margin-left: 0;
                    font-size: 0;
                    width: 22px;
                    height: 22px;
                    display: block;
                    background: url(../img/icon-home.png) center center/22px no-repeat;
                }
            }

            >li:last-child {
                background: url(../img/icon-arr-down.png) right 8px center no-repeat;
                display: block;
                position: relative;

                &:hover .sub-menu {
                    display: block;
                }

                a {
                    padding-right: 21px;
                }
            }
        }

        .sub-menu {
            position: absolute;
            background: #F3F8FF;
            display: none;
            width: 200px;
            right: 0;
            box-shadow: rgba(100, 100, 111, 0.2) 0px 7px 29px 0px;

            a {
                margin-left: 0;
                padding: 8px 20px;
                text-align: center;
            }

            a:hover {
                color: #0073BF;
                background: rgba(0, 0, 0, 0.03);
            }
        }
    }

    // list-story
    .list-story {
        background: #F3F8FF;
        padding: 20px 0;
        margin-top: 12px;

        .scroll {
            overflow-x: scroll;
            overflow-y: hidden;
            padding-bottom: 8px;
        }

        .story {
            padding-left: 50px;

            &:first-child {
                padding-left: 0;
            }

            &__thumb {
                width: 110px;
                height: 80px;
                float: left;
                margin-right: 16px;
            }

            &__heading a {
                font-style: normal;
                font-weight: 500;
                font-size: 13px;
                line-height: 18px;
                color: #343A40;
                @include Roboto();
                width: 255px;

                a:hover {
                    color: #0073BF !important;
                }
            }

            &__time {
                font-style: normal;
                font-weight: normal;
                font-size: 11px;
                line-height: 22px;
                color: #ADB5BD;
                padding-top: 4px;
                @include Roboto();
            }
        }
    }

    // INFO :  style for scroll
    .scroll::-webkit-scrollbar-track {
        -webkit-box-shadow: inset 0 0 4px #f3f8ff;
        border-radius: 10px;
        background-color: #f3f8ff;
    }

    .scroll::-webkit-scrollbar {
        height: 4px;
        background: #ddd;
    }

    .scroll::-webkit-scrollbar-thumb {
        border-radius: 10px;
        background-color: #ddd;
    }

}