@font-face {
  font-family: 'Pattaya';
  src: url('../fonts/Pattaya/Pattaya-Regular.ttf');
  font-weight: 400;
}

@font-face {
  font-family: 'SairaStencil';
  src: url('../fonts/Saira_Stencil_One/SairaStencilOne-Regular.ttf');
  font-weight: 400;
}

@mixin Pattaya {
  font-family: 'Pattaya';
}

@mixin SairaStencil {
  font-family: 'SairaStencil'
}

// mixin fonts
@mixin Roboto {
  font-family: 'Roboto';
}

@mixin NotoSerif {
  font-family: 'NotoSerif';
}

@mixin RobotoItalic {
  font-family: 'Roboto';
  src: url('../fonts/Roboto/Roboto-BoldItalic.ttf');
  font-weight: 400;
}

@mixin PlayfairDisplayItalic {
  font-family: 'PlayfairDisplay';
  src: url('../fonts/PlayfairDisplay/PlayfairDisplay-BoldItalic.ttf');
  font-weight: 700;
}

@mixin PlayfairDisplayBoldItalic {
  font-family: 'PlayfairDisplay';
  src: url('../fonts/PlayfairDisplay/PlayfairDisplay-BoldItalic.ttf');
  font-weight: 700;
}

@mixin PlayfairDisplay {
  font-family: 'PlayfairDisplay';
}

@mixin border-bottom {
  &:before {
    position: absolute;
    content: "";
    height: 5px;
    width: 45px;
    background: #bd1723;
    bottom: 0;
    left: 0;
    display: block;
  }
}

@keyframes slideDown {
  from {
    transform: translateY(-5px);
    opacity: 0;
  }

  to {
    transform: translateY(0);
    opacity: 1;
  }
}

// style nav tab  
.tab-pane {
  display: none;

  &.active {
    display: block;
  }
}

// style index page
.index-page {
  display: flex;
  justify-content: space-around;

  p {
    font-size: 20px;
    font-weight: 600;
  }

  ul {
    margin-top: 5px;

    a {
      margin-top: 10px;
      display: block;
    }
  }
}

.box-heading,
.zone-heading {
  .heading:hover {
    color: $primary !important;
  }
}

// ===================== style modal 
.modal-noti {
  .title {
    font-style: normal;
    font-weight: normal;
    font-size: 16px;
    line-height: 23px;
    text-align: center;
    color: #6C757D;
    @include Roboto();
  }
}

// style global
.modal-forgot-password,
.modal-register,
.modal-login {
  padding-top: 200px;

  .modal-body {
    padding: 22px 28px 0 30px;
  }

  .modal-footer {
    height: 80px;
    border-top: 1px solid #E9ECEF;
    display: flex;
    justify-content: center;

    .btn-1,
    .btn-2 {
      padding: 10px;
      width: 240px;
      text-transform: uppercase;
      border-radius: 4px;
      border: 1px solid #E9ECEF;
      height: 42px;
      color: #343A40;
      font-weight: 500;
      @include Roboto();
    }

    .btn-1 {
      background: $white;
      font-weight: normal;
      font-size: 14px;
      line-height: 16px;
      margin-right: 23px;
      color: #000000;
      outline: none;
    }

    .btn-2 {
      background: #E9ECEF;
    }
  }

  .modal-header {
    height: 70px;
    background: #E21F26;
    display: flex;
    align-items: center;
    justify-content: center;

    .modal-title {
      font-style: normal;
      font-weight: normal;
      font-size: 25px;
      line-height: 34px;
      text-transform: uppercase;
      color: $white;
    }
  }

  form {
    margin-top: 23px;
    margin-bottom: 23px;
    width: 100%;

    p {
      font-style: normal;
      font-weight: normal;
      font-size: 12px;
      line-height: 14px;
      color: #495057;
      margin-top: 9px;
      text-align: left;
      @include Roboto();

      a {
        color: #3D8BFD;
        text-decoration: underline;
        margin-left: 8px;
      }

      &:first-of-type {
        margin-top: 23px;
      }
    }

    input {
      border: none;
      outline: none;
      width: 100%;
      height: 30px;


      &::placeholder {
        font-style: normal;
        font-weight: normal;
        font-size: 14px;
        line-height: 16px;
        color: #CED4DA;
        @include Roboto();
      }
    }

    legend {
      width: fit-content;
      margin-bottom: 0;
      font-style: normal;
      font-weight: normal;
      font-size: 12px;
      line-height: 14px;
      color: #495057;
      @include Roboto();
    }

    fieldset {
      border: 1px solid #CED4DA;
      border-radius: 4px;
      padding: 0 15px;
      height: 52px;
      margin-top: 20px;

      &:first-of-type {
        margin-top: 0;
      }
    }
  }

  .g-recaptcha {
    margin-top: 24px;
  }
}

