.modal-comments,
.modal-noti {
  legend {
    width: fit-content;
    margin-bottom: 0;
    font-style: normal;
    font-weight: 400;
    font-size: 12px;
    line-height: 14px;
    color: #495057;
    @include Roboto();
  }

  fieldset {
    border: 1px solid #ced4da;
    border-radius: 4px;
    padding: 0 15px;
    height: 52px;
    margin-top: 20px;
  }

  form>fieldset {
    height: 140px;

    >textarea {
      height: calc(100% - 8px) !important;
      padding-top: 10px;
    }
  }

  .modal-content {
    border: none;
  }

  .modal-dialog {
    max-width: 648px;
    position: relative;
    top: 10%;

    .modal-header {
      justify-content: center;
      padding: 15px;
      border-bottom: 1px solid var(--fourth);
      background: #E21F26;
      height: 70px;
      align-items: center;

      .modal-title {
        font-style: normal;
        font-weight: 500;
        font-size: 24px;
        line-height: 33px;
        text-transform: uppercase;
        color: #FFFFFF;
      }

      .close {
        padding: 0;
        font-size: 24px;
        opacity: .5;
        margin-top: -2px;
        color: #000;
        margin-right: 0;
      }
    }

    .modal-body {
      padding: 27px 52px;
      font-size: 18px;
      line-height: 24px;

      .story__title {
        padding-bottom: 8px;
        margin-bottom: 10px;
        font-size: 14px;
        line-height: 1.4;
        font-weight: normal;
      }
    }

    form {
      .wrap {
        display: flex;
        justify-content: space-between;
        align-items: center;

        fieldset {
          width: 260px;
        }
      }

      .group {
        margin-top: 10px;

        &:first-child {
          margin-top: 0;
        }
      }

      label {
        font-size: 14px;
        line-height: 1.4;
        font-weight: 700;
      }

      input,
      textarea {
        border: none;
        outline: 0;
        width: 100%;
        height: 30px;
        font-size: 14px;
        line-height: 16px;

        &::placeholder {
          font-style: normal;
          font-weight: normal;
          font-size: 14px;
          line-height: 16px;
          color: #CED4DA;
          @include Roboto();
        }
      }
    }

    .modal-footer {
      display: flex;
      align-items: center;
      justify-content: center;
      height: 80px;

      .btn-submit,
      .bnt-cancel {
        border: 1px solid #ccc;
        background: var(--white);
        border-radius: 5px;
        padding: 9.5px 10px;
        cursor: pointer;
        margin: 0 10px;
        white-space: nowrap;
        width: 240px;
        outline: none;
        font-style: normal;
        font-weight: 500;
        font-size: 16px;
        line-height: 19px;
        text-transform: uppercase;
        color: #343A40;
        @include Roboto();
      }

      .btn-submit {
        border: 1px solid #E9ECEF;
        box-sizing: border-box;
        border-radius: 4px;
        background: #E21F26;
        border-radius: 4px;
        color: white;
      }
    }
  }
}

.modal-vote {
  .modal-dialog {
    max-width: 500px;
    position: relative;
    top: 10%;

    .modal-header {
      padding: 15px;
      border-bottom: 1px solid var(--fourth);

      .modal-title {
        color: $primary;
        font-weight: 700;
        font-size: 20px;
        line-height: 24px;
        text-transform: uppercase;
        display: inline-block;
        font-family: 'Noto Serif', serif;
      }

      .close {
        padding: 0;
        font-size: 24px;
        opacity: .5;
        margin-top: -2px;
        color: #000;
        margin-right: 0;
      }
    }

    .modal-body {
      padding: 15px;
      font-size: 18px;
      line-height: 24px;

      .scroll_color {
        width: 50%;
        float: left;
        margin: 5px 0;
        height: 5px;
        background: #ccc;
      }

      .story__title {
        padding-bottom: 8px;
        margin-bottom: 10px;
        border-bottom: 1px dotted #c2c2c2;
        font-size: 14px;
        line-height: 1.4;
        font-weight: 700;
      }

      .bg_center_scroll {
        position: relative;
        background: $primary;
        float: left;
        height: 5px;
      }

      .txt_number_ketqua {
        display: inline-block;
        padding: 5px 0 0;
        position: absolute;
        right: 0;
        bottom: -20px;
        font: 11px arial;
        color: $primary;
      }

      .list_vote li {
        width: 100%;
        float: left;
        margin: 0 0 10px;
        padding: 0 0 15px;
        border-bottom: 1px dotted #ccc;
      }

      .list_vote .rsV_left {
        width: 30%;
        float: left;
        font: 13px arial;
        color: #555;
        padding-right: 15px;
      }

      .rsV_right {
        width: 18%;
        float: right;
        font: 12px arial;
        color: #999;
        text-align: right;

        label {
          color: #000;
        }
      }

      .total_vote {
        float: right;
        font: 400 13px arial;
        color: #666;

        label {
          color: $primary;
          font-weight: 700;
        }
      }
    }
  }
}

