.search-page {

  .wrap-banner {
    background: url(../img/banner-search.png) center center/100% 100% no-repeat;
    width: 100%;
    height: 429px;
    padding-top: 120px;
    text-align: center;

    .heading {
      font-style: normal;
      font-weight: 300;
      font-size: 50px;
      line-height: 59px;
      text-transform: uppercase;
      color: #343A40;
      text-shadow: 4px 4px 0px rgba(0, 0, 0, 0.1);
      @include Roboto();
    }

    .wrap-search {
      margin-top: 32px;
      position: relative;

      input {
        background: #FFFFFF;
        border: 1px solid #E9ECEF;
