        box-shadow: 6px 8px 0px rgba(0, 0, 0, 0.05);
        width: 700px;
        height: 64px;
        padding: 0 80px 0 20px;

        &::placeholder {
          font-style: normal;
          font-weight: normal;
          font-size: 16px;
          line-height: 19px;
          display: flex;
          align-items: center;
          color: #ADB5BD;
          @include Roboto();
        }
      }

      i {
        position: absolute;
        background: #E21F26;
        border-radius: 4px;
        padding: 16px;
        font-size: 24px;
        margin-left: -60px;
        top: 4px;
        color: white;
        cursor: pointer;
      }
    }
  }

  .story {
    margin-top: 20px;
    padding-top: 32px;
    margin-top: 32px;
    border-top: 1px solid #ddd;
    overflow: auto;

    &:nth-of-type(1) {
      margin-top: 0;
      padding-top: 0;
      border-top: 0;
    }

    &__thumb {
      width: 297px;
      height: 198px;
      float: left;
      margin-right: 24px;
    }

    &__heading a {
      font-style: normal;
      font-weight: 600;
      font-size: 24px;
      line-height: 32px;
      color: #000000;
      @include PlayfairDisplay();
      padding-top: 13px;
    }

    &__meta {
      display: flex;
      align-items: center;
      margin-top: 10px;


      .text {
        padding-left: 32px;
        font-style: normal;
        font-weight: normal;
        font-size: 12px;
        line-height: 22px;
        color: #ADB5BD;
        @include Roboto();
        text-transform: capitalize;

        &:first-child {
          &:hover {
            color: $primary !important;

            i {
              background: $primary;
            }
          }
        }

        &:hover {
          color: $primary !important;
        }

        &:first-child {
          padding-left: 0;
        }
      }

      .text1 i {
        width: 18px;
        height: 18px;
        background: #CED4DA;
        border-radius: 10px;
        color: white;
        margin-right: 8px;
        font-size: 9px;
        text-align: center;
        padding-top: 4px;
      }

      .text2 i,
      .text3 i {
        font-size: 14px;
        margin-right: 8px;
      }

      .text3 {
        text-transform: uppercase;
      }
    }

    &__summary {
      font-style: normal;
      font-weight: normal;
      font-size: 13px;
      line-height: 22px;
      color: #6C757D;
      @include Roboto();
      margin-top: 10px;
    }
  }

  .result {
    font-style: normal;
    font-weight: normal;
    font-size: 15px;
    line-height: 20px;
    display: flex;
    align-items: center;
    color: #6C757D;
    @include Roboto();
    margin-bottom: 20px;

    span {
      @include Roboto();
      font-weight: bold;
      padding: 0 3px;
    }
  }
}

