      }

      .group-select {
        display: flex;
        justify-content: space-between;
        width: 100%;

        select {
          width: 200px;
          height: 44px;
          background: url(../img/arrow_ver3.png) 175px center no-repeat;
        }
      }

      select {
        background: url(../img/arrow_ver3.png) 655px center no-repeat;
      }
    }
  }

  .tab4 {
    .box-content {
      .form-field {
        display: flex;
        align-items: center;
        margin-top: 32px;

        &:first-child {
          label {
            &::before {
              right: 86px;
            }
          }
        }

        &:nth-child(3) {
          label {
            &::before {
              right: 13px;
            }
          }
        }
      }

      label {
        width: 250px;
        position: relative;



        &::before {
          position: absolute;
          top: 0;
          right: 76px;
          content: '*';
