.podcast-page {
  .wrap-slide {
    background: #F8F9FA;
    height: 120px;
    padding: 20px 90px;
    display: flex;
    justify-content: space-between;

    @media (min-width:1441px) {
      padding: 20px 200px;
    }
  }

  .auto-play {
    background: #FFFFFF;
    border: 1px solid #E9ECEF;
    box-sizing: border-box;
    border-radius: 8px;
    padding: 16px;
    width: 163px;
    height: 80px;

    .text-1 {
      font-style: normal;
      font-weight: 500;
      font-size: 16px;
      line-height: 20px;
      text-transform: uppercase;
      color: #000000;
      margin-top: 0 !important;
    }

    .text-2 {
      width: 105px;
      height: 32px;
      display: flex;
      font-style: normal;
      font-weight: 400;
      font-size: 14px;
      line-height: 20px;
      color: #000;
      margin-top: 4px !important;
      background: url(../img/icon-control.png) left center no-repeat;
      align-items: center;
      justify-content: end;
      @include Roboto();
      cursor: pointer;
    }
  }
}

.podcast-page {
  .podcast {
    margin-top: 23px;
    padding: 70px 0 33px;
    background: url(../img/bg-podcast.png) center center/100% 100% no-repeat;
    text-align: center;

    :not(.player-podcast) {
      max-width: 695px;
      margin: 0 auto;
    }
  }

  .date,
  .hour {
    font-style: normal;
    font-weight: 500;
    font-size: 16px;
    line-height: 22px;
    color: #FFFFFF;
    position: relative;
    padding: 0 10px;
    @include NotoSerif();

    &::before {
      position: absolute;
      content: '';
      width: 1px;
      height: 16px;
      background: #fff;
      left: 0;
      top: 3px;
    }
  }

  .date::before {
    display: none;
  }

  .text-1 {
    font-style: normal;
    font-weight: bold;
    font-size: 38px;
    line-height: 46px;
    text-align: center;
    color: #FFFFFF;
    margin-top: 12px !important;
    @include PlayfairDisplay();
  }

  .text-2 {
    font-style: italic;
    font-weight: normal;
    font-size: 18px;
    line-height: 28px;
    text-align: center;
    color: #FFFFFF;
    margin-top: 20px !important;
    @include NotoSerif();
  }

  .wrap-features {
    display: block;
    margin-top: 20px !important;

    >* {
      margin: 0 6px !important;
    }

    .copy-link,
    .mail {
      background-color: rgba(255, 255, 255, 0.4);
    }
  }

  .player-podcast {
    display: flex;
    align-items: center;
    justify-content: center;
    margin-top: 63px;
    background: rgba(255, 255, 255, 0.1);
    width: 100%;
    height: 90px;

    .region-1,
    .region-2,
    .region-3 {
      margin: 0;
      max-width: 100%;
    }

    .region-1 {
      display: flex;
      align-items: center;

      .btn-prev {
        width: 36px;
        height: 40px;
        background: url(../img/prev-audio.png) center center no-repeat;
      }

      .btn-pause {
        margin: 0 23px;

        i {
          font-weight: 900;
          font-size: 60px;
          line-height: 60px;
          color: #FFFFFF;
        }
      }

      .btn-next {
        width: 36px;
        height: 40px;
        background: url(../img/next-audio.png) center center no-repeat;
      }

      .fa-play-circle {
        display: none;
      }
    }

    .region-2 {
      width: 700px;
      margin: 0 50px 0 75px;
      position: relative;

      .line-1 {
        background: rgba(255, 255, 255, 0.2);
        height: 2px;
        display: block;
        cursor: pointer;
      }

      .line-2 {
        background: rgba(255, 255, 255, .6);
        height: 2px;
        display: block;
        position: absolute;
        width: 400px;
        top: 50%;
        transform: translateY(-50%);
        left: 2px;
        cursor: pointer;
      }

    }

    .region-3 i {
      font-weight: 300;
      font-size: 28px;
      line-height: 28px;
      text-align: center;
      color: #FFFFFF;
      cursor: pointer;
    }
  }
}

.podcast-slide {
  max-width: 1000px;

  .prevArrow,
  .nextArrow {
    cursor: pointer;
    width: 45px;
    height: 33px;
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
  }

  .prevArrow {
    background: url(../img/podcast-right.png) center center no-repeat;
    right: -50px;

    @media (min-width:1441px) {
      right: -160px;
    }
  }


  .nextArrow {
    left: -320px;
    background: url(../img/podcast-left.png) center center no-repeat;

    @media (min-width:1441px) {
      left: -660px;
    }
  }

  .story {
    &__thumb {
      width: 110px;
      height: 80px;
      float: left;
      margin-right: 16px;
    }

    &__heading {
      max-width: 290px;
      font-style: normal;
      font-weight: 500;
      font-size: 14px;
      line-height: 20px;
      color: #212529;
    }

    &__time {
      font-style: normal;
      font-weight: normal;
      font-size: 11px;
      line-height: 22px;
      color: #ADB5BD;
      @include Roboto();
    }
  }
}

