@import "../../scss/bootstrap-4.1.2/scss/functions";
@import "../../scss/variables";
@import "../../scss/mixins/font-style";
@import "../../scss/mixins/story-image";
@import "../../scss/mixins/no-padding-margin-border";

@mixin PlayfairDisplay {
  font-family: 'PlayfairDisplay';
}

@mixin Roboto {
  font-family: 'Roboto';
}

.box-style-12 {
  .story-large {
    overflow: auto;
    box-sizing: border-box;
    border-radius: 4px;
    margin-top: 44px;
    position: relative;
    padding-bottom: 8px;

    &:first-of-type {
      margin-top: 0;

      &::before {
        display: none;
      }
    }

    &::before {
      position: absolute;
      content: '';
      width: 100%;
      height: 1px;
      background: $border-color;
      top: -24px;
      display: block;
      left: 0;
    }

    .story__thumb {
      width: 220px;
      height: 147px;
      float: left;
      margin-right: 16px;
      box-shadow: 4px 4px #ADB5BD, 8px 8px #E9ECEF;
    }

    .story__meta a {
      font-style: normal;
      font-weight: 600;
      font-size: 12px;
      line-height: 20px;
      text-transform: uppercase;
      color: #BD1723;
      @include Roboto();
    }

    .story__heading a {
      font-style: normal;
      font-weight: 600;
      font-size: 18px;
      line-height: 26px;
      color: #000000;
      @include PlayfairDisplay();
      padding-top: 4px;
    }


  }

  .story__summary {
    font-style: normal;
    font-weight: normal;
    font-size: 13px;
    line-height: 22px;
    color: #6C757D;
    height: 70px;
    width: 63%;
    margin-top: 4px;
    max-height: 90px;
    @include Roboto();
    overflow: hidden;
    overflow: hidden;
    text-overflow: ellipsis;
    display: -webkit-box;
    -webkit-line-clamp: 3;
    line-clamp: 3;
    -webkit-box-orient: vertical;
  }

  .story-small {
    overflow: auto;
    margin-top: 24px;
    padding-top: 24px;
    border-top: 1px solid $border-color;

    &:first-child {
      margin-top: 0px;
      padding-top: 0px;
      border-top: none;
    }

    .story__thumb {
      width: 220px;
      height: 147px;
      margin-right: $spacer-3;
      float: left;
    }

    .story__meta a {
      font-style: normal;
      font-weight: normal;
      font-size: 12px;
      line-height: 20px;
      text-transform: uppercase;
      color: #6C757D;
      @include Roboto();
    }

    .story__heading a {
      font-style: normal;
      font-weight: 600;
      font-size: 18px;
      line-height: 26px;
      color: #000000;
      padding-top: 4px;
      @include PlayfairDisplay();
    }

  }
}