.box-style-20 {
  width: 231px;

  .box-heading {
    a {
      font-style: normal;
      font-weight: bold;
      font-size: 16px;
      line-height: 20px;
      color: #000000;
      overflow: hidden;
      text-overflow: ellipsis;
      display: -webkit-box;
      -webkit-line-clamp: 1;
      line-clamp: 1;
      -webkit-box-orient: vertical;
    }
  }

  .box-content {
    margin-top: 12px;

    .story:first-child {
      margin-top: 0;

      .story__heading a {
        padding-top: 8px;
        font-weight: bold;
      }
    }

    .story {
      margin-top: 12px;
      border-top: 1px solid #E9ECEF;
      padding-top: 12px;

      &:first-child {
        padding-top: 0;
        border-top: none;
      }

      &__thumb {
        height: 154px;
        width: 100%;
      }

      &__heading a {
        font-style: normal;
        font-weight: 500;
        font-size: 14px;
        line-height: 22px;
        color: #343A40;
        display: block;
        font-weight: normal;
      }

      &__meta {
        font-style: normal;
        font-weight: bold;
        font-size: 16px;
        line-height: 20px;
        color: #000000;
      }


    }
  }
}