@import "../../scss/bootstrap-4.1.2/scss/functions";
@import "../../scss/variables";
@import "../../scss/mixins/font-style";
@import "../../scss/mixins/story-image";
@import "../../scss/mixins/no-padding-margin-border";

@mixin PlayfairDisplay {
  font-family: 'PlayfairDisplay';
}

@mixin border-bottom {
  &:before {
    position: absolute;
    content: "";
    height: 5px;
    width: 45px;
    background: #bd1723;
    bottom: 0;
    left: 0;
    display: block;
  }
}

.box-style-17 {
  margin-top: 66px;

  .box-heading {
    border-top: 8px solid #F8F9FA;

    a {
      padding-top: 20px;
      font-style: normal;
      font-weight: 900;
      font-size: 22px;
      line-height: 29px;
      text-transform: uppercase;
      color: #343A40;
      position: relative;
      display: block;
      padding-bottom: 13px;
      @include PlayfairDisplay();

      @include border-bottom();
    }
  }

  .box-content {
    justify-content: space-between;
    margin-top: $spacer-3;
  }

  .rank-1 {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    position: relative;

    &::before {
      position: absolute;
      right: 50%;
      height: 100%;
      width: 1px;
      content: "";
      background-color: #DEE2E6;
    }

    .story {
      margin-top: $spacer-3;
      padding-top: $spacer-3;
      border-top: 1px solid $border-color;
      width: 265px;

      &:nth-child(-n+2) {
        border-top: 0;
        padding-top: 0;
        margin-top: 0;
      }

      &__thumb {
        width: 264px;
        height: 177px;
        position: relative;
      }

      &__heading a {
        font-style: normal;
        font-weight: 600;
        font-size: 16px;
        line-height: 26px;
        color: #212529;
        padding-top: 8px;
        @include PlayfairDisplay();
        position: relative;
      }

      span {
        position: absolute;
        left: 8px;
        bottom: 8px;
        width: 30px;
        height: 30px;
        border-radius: 50%;
        font-size: 0;
        background-size: 22px;
      }
    }
  }

  .rank-2 {
    .story {
      &__thumb {
        width: 693px;
        height: 389.81px;
        position: relative;
      }

      &__heading a {
        font-style: normal;
        font-weight: bold;
        font-size: 24px;
        line-height: 30px;
        color: #212529;
        padding-top: $spacer-3;
        @include PlayfairDisplay();
      }

      span {
        position: absolute;
        left: $spacer-3;
        bottom: $spacer-3;
        width: 60px;
        height: 60px;
        border-radius: 50%;
        font-size: 0;
      }

      &__summary {
        margin-top: $spacer-1;
        font-style: normal;
        font-weight: normal;
        font-size: 13px;
        line-height: 22px;
        color: #6C757D;
      }
    }
  }
}