@mixin Roboto {
    font-family: 'Roboto';
}

.breadcrumbs {
    display: flex;

    .wrap-text:last-child .text {
        &::after {
            display: none;
        }
    }

    .wrap-text .text {
        font-style: normal;
        font-weight: 500;
        font-size: 15px;
        line-height: 18px;
        text-transform: uppercase;
        color: #888888;
        @include Roboto();
        position: relative;
        padding-right: 17px;
        margin-right: 12px;
        padding-left: 0;

        &.active,
        &:hover {
            color: #555 !important;
        }



        &::after {
            content: '';
            position: absolute;
            top: 6px;
            border: solid #888;
            border-width: 0 1.2px 1.2px 0;
            display: inline-block;
            padding: 2.5px;
            right: 0;
            transform: rotate(-45deg);
            -webkit-transform: rotate(-45deg);
        }
    }
}