@import "../../scss/bootstrap-4.1.2/scss/functions";
@import "../../scss/variables";
@import "../../scss/mixins/font-style";
@import "../../scss/mixins/story-image";
@import "../../scss/mixins/no-padding-margin-border";

@mixin PlayfairDisplay {
  font-family: 'PlayfairDisplay';
}

@mixin Roboto {
  font-family: 'Roboto';
}

.box-style-21 {
  overflow: hidden;

  .box-heading {
    .wrap-heading {
      position: relative;

      &::before,
      &:after {
        position: absolute;
        height: 11px;
        background: #E6EEF6;
        content: "";
        width: 50%;
        top: 50%;
        transform: translateY(-50%);
        z-index: -1;

      }
    }

    a {
      font-style: normal;
      font-weight: 900;
      font-size: 20px;
      line-height: 26px;
      color: #BD1723;
      @include PlayfairDisplay();
      background: $white;
      margin-left: 20px;
      padding: 0 10px;
      text-transform: uppercase;
    }
  }

  .box-content {
    margin-top: 10px;

    .rank-1 {
      .story {
        &__time {
          font-style: normal;
          font-weight: normal;
          font-size: 12px;
          line-height: 22px;
          color: #ADB5BD;
          @include Roboto();
          display: flex;
          align-items: center;

          i {
            margin-right: 8px;
            font-style: normal;
            font-weight: normal;
            font-size: 13px;
            line-height: 13px;
            color: #495057;
          }
        }


        &__heading a {
          padding-top: 4px;
          font-style: normal;
          font-weight: bold;
          font-size: 16px;
          line-height: 23px;
          color: #212529;
          @include PlayfairDisplay();

          i {
            font-style: normal;
            font-weight: 900;
            font-size: 12px;
            line-height: 22px;
            color: #CED4DA;
            margin-left: 10px;
          }
        }

        &__summary {
          font-style: normal;
          font-weight: normal;
          font-size: 13px;
          line-height: 22px;
          color: #6C757D;
          margin-top: 8px;
          @include Roboto();
        }
      }
    }

    .rank-2 {
      margin-top: $spacer-3;
      padding-top: $spacer-3;
      border-top: 1px solid $border-color;

      .story {
        margin-top: $spacer-3;
        padding-top: $spacer-3;
        border-top: 1px solid $border-color;

        &:first-child {
          margin-top: 0;
          padding-top: 0;
          border-top: 0;
        }

        &__time {
          font-style: normal;
          font-weight: normal;
          font-size: 12px;
          line-height: 22px;
          color: #ADB5BD;
          @include Roboto();
          display: flex;
          align-items: center;

          i {
            margin-right: 8px;
            font-style: normal;
            font-weight: normal;
            font-size: 13px;
            line-height: 13px;
            color: #495057;
          }
        }


        &__heading a {
          font-style: normal;
          font-weight: 500;
          font-size: 14px;
          line-height: 20px;
          color: #212529;
          padding-top: 4px;
        }
      }
    }
  }
}