@import "../../scss/bootstrap-4.1.2/scss/functions";
@import "../../scss/variables";
@import "../../scss/mixins/font-style";
@import "../../scss/mixins/story-image";
@import "../../scss/mixins/no-padding-margin-border";

@mixin Roboto {
    font-family: 'Roboto';
}

@keyframes slideRight {
    from {
        transform: translateX(-5px);
        opacity: 0;
    }

    to {
        transform: translateX(0);
        opacity: 1;
    }
}

.region-1 {
    .wrap {
        width: 126px;
    }
}

.wrap-menu {
    transition: .2s;

    &:hover {
        background: #A6111C;
        border-color: #A6111C;
    }
}

.region-nav {
    height: 100%;
    background: rgba(0, 0, 0, .2);
    margin-left: -12px;
    display: none;
    margin-top: 6px;
    position: absolute;
    width: calc(100% + 10px);
    height: 100%;
    top: 42px;
    z-index: 99;
    animation-name: slideRight;
    animation-duration: 1s;
    position: fixed;
}

.nav-wrapper {
    background: #A6111C;
    box-shadow: 4px 4px 96px rgba(0, 0, 0, 0.25);
    width: 231px;
    height: 100%;
    color: $white;
    padding: 16px 0;

    ul.menu-1 {
        li {
            position: relative;
            cursor: pointer;
            display: flex;
            align-items: center;
            justify-content: space-between;

            &:hover {
                background: rgba(0, 0, 0, .1);
            }

            &:hover>ul {
                display: block;
            }
        }

        a {
            font-weight: 500;
            font-size: 13px;
            line-height: 15px;
            text-transform: uppercase;
            color: $white;
            padding: 15px 0;
            display: block;
            width: 100%;
        }

        i {
            padding: 12.5px 15px;
            font-size: 13px;
            line-height: 20px;
            color: #CCCCCC;
            font-weight: normal;
        }
    }

    ul.menu-1,
    ul.menu-2,
    ul.menu-3 {
        position: relative;
        margin-top: 8px;
        margin-bottom: 16px;

        &::before {
            top: -8px;
            display: block;
            border-top: 1px solid rgba(255, 255, 255, .2);
            position: absolute;
            content: "";
            height: 1px;
            width: calc(100% - 12px);
        }

        &:first-child {
            margin-top: 0;

            &::before {
                display: none;
            }
        }

        a {
            font-weight: 500;
            font-size: 13px;
            line-height: 15px;
            text-transform: uppercase;
            color: $white;
            padding: 15px 0;
            display: block;
            width: 100%;
            padding-left: 20px;
            @include Roboto();
        }
    }

    .menu-1 .sub-menu {
        margin-top: 0;
        margin-bottom: 0;
        border-top: none;
        position: absolute;
        display: none;
        right: -195px;
        background: #A6111C;
        box-shadow: 10px 0px 50px rgba(0, 0, 0, 0.15);
        top: 20px;
        width: 195px;
        padding: 12px 0 12px;
        animation: slideRight 1s;

        &::before {
            display: none;
        }

        a {
            text-transform: none;
            font-style: normal;
            font-weight: 500;
            font-size: 13px;
            line-height: 15px;
            color: $white;
            padding: 10.5px 12px 10.5px 20px;

            &:hover {
                background: rgba(0, 0, 0, .01);
            }
        }
    }

    .menu-2 .sub-menu {
        a {
            font-weight: 500;
            font-size: 12px;
            line-height: 14px;
            text-transform: uppercase;
            color: #FFFFFF;
            opacity: 0.7;
            padding: 11px 0 11px 32px;
            @include Roboto;
        }
    }
}