@import "../../scss/bootstrap-4.1.2/scss/functions";
@import "../../scss/variables";
@import "../../scss/mixins/font-style";
@import "../../scss/mixins/story-image";
@import "../../scss/mixins/no-padding-margin-border";

@mixin NotoSans {
    font-family: 'NotoSans';
}

@mixin Roboto {
    font-family: 'Roboto';
}

.site-footer-thoinay {
    margin-top: 61px;

    ul {
        display: flex;
        align-items: center;
        overflow-x: scroll;
        overflow-y: hidden;

        li:first-child {
            a {
                margin-left: 0;
            }
        }

        a {
            font-style: normal;
            font-weight: 600;
            font-size: 18px;
            line-height: 25px;
            text-align: right;
            text-transform: uppercase;
            color: #343A40;
            @include NotoSans();
            white-space: nowrap;
            margin-left: 40px;

            &:hover {
                color: #0073bf;
            }
        }
    }

    .wrap {
        display: flex;
        justify-content: space-between;
        padding-bottom: 24px;
        padding-top: 17px;
        border-top: 8px solid $border-color;
        align-items: center;

        .logo {
            background: url(../img/logo-thoinay.png) center center no-repeat;
            width: 160px;
            height: 111px;
        }

        .info {
            text-align: right;

            >* {
                font-style: normal;
                font-weight: normal;
                font-size: 16px;
                line-height: 26px;
                text-align: right;
                color: #212529;
                @include Roboto();
            }
        }
    }

    // INFO :  style for scroll
    .scroll::-webkit-scrollbar-track {
        -webkit-box-shadow: inset 0 0 4px #f3f8ff;
        border-radius: 10px;
        background-color: #f3f8ff;
    }

    .scroll::-webkit-scrollbar {
        height: 4px;
        background: #ddd;
    }

    .scroll::-webkit-scrollbar-thumb {
        border-radius: 10px;
        background-color: #ddd;
    }
}