@import "../../scss/bootstrap-4.1.2/scss/functions";
@import "../../scss/variables";
@import "../../scss/mixins/font-style";
@import "../../scss/mixins/story-image";
@import "../../scss/mixins/no-padding-margin-border";

.box-style-18 {
  width: 297px;

  .box-heading {
    a {
      font-style: normal;
      font-weight: bold;
      font-size: 16px;
      line-height: 20px;
      color: #000000;
      text-transform: uppercase;
      position: relative;
      padding-left: 23px;

      &::before {
        left: 6px;
        width: 5px;
        height: 100%;
        position: absolute;
        top: 0;
        content: "";
        background: $primary;
        transform: skew(-24deg);
      }
    }
  }

  .box-content {
    margin-top: $spacer-3;

    .story:first-child {
      border-top: none;
      padding-top: 0;
      margin-top: 0;

      &::before {
        display: none;
      }

      .story__thumb {
        width: 297px;
        height: 198px;
      }

      .story__heading a {
        font-style: normal;
        font-weight: bold !important;
        font-size: 14px;
        line-height: 22px;
        color: #212529;
        padding-top: 8px;
      }

    }

    .story {
      position: relative;
      padding-top: 12px;
      margin-top: $spacer-3;

      &::before {
        position: absolute;
        content: "";
        width: 87px;
        top: 0;
        height: 1px;
        background: $border-color;
        left: 0;
      }

      &__heading a {
        font-style: normal;
        font-weight: 500;
        font-size: 14px;
        line-height: 22px;
        color: #343A40;
      }

    }

  }
}