@import "../../scss/bootstrap-4.1.2/scss/functions";
@import "../../scss/variables";
@import "../../scss/mixins/font-style";
@import "../../scss/mixins/story-image";
@import "../../scss/mixins/no-padding-margin-border";

@mixin Roboto {
  font-family: 'Roboto';
}

.heading-page {
  margin-top: 24px;

  .heading {
    font-style: normal;
    font-weight: 600;
    font-size: 30px;
    line-height: 35px;
    text-transform: uppercase;
    color: #212529;
    display: block;
    cursor: pointer;
    @include Roboto();

    &:hover {
      color: $primary;
    }
  }

  .list-news {
    margin-top: $spacer-2;
    display: flex;

    a {
      font-style: normal;
      font-weight: normal;
      font-size: 12px;
      line-height: 14px;
      color: #343A40;
      position: relative;
      padding-left: 11px;
      margin-right: 20px;
      text-transform: uppercase;
      display: block;
      @include Roboto();

      &:hover,
      &.active {
        color: $primary;

        &::after {
          background: $primary;
        }
      }

      &::after {
        top: 50%;
        transform: translateY(-50%);
        border-radius: 50%;
        position: absolute;
        content: "";
        width: 3px;
        height: 3px;
        background-color: #6C757D;
        left: 0;
      }
    }
  }
}

.section-cate {

  .heading-page a.active,
  .heading-page a:hover {
    color: #0073bf !important;

    &::after {
      background: #0073bf !important;
    }
  }
}