@import "../../scss/bootstrap-4.1.2/scss/functions";
@import "../../scss/variables";
@import "../../scss/mixins/font-style";
@import "../../scss/mixins/story-image";
@import "../../scss/mixins/no-padding-margin-border";

@mixin Roboto {
  font-family: 'Roboto';
}

@mixin RobotoItalic {
  font-family: 'Roboto';
  src: url('../fonts/Roboto/Roboto-BoldItalic.ttf');
  font-weight: 400;
}

.box-style-2 {
  .story {
    padding-top: 16px;
    margin-top: 20px;
    position: relative;

    &:first-child {
      margin-top: 0;
      padding-top: 0;

      &::before {
        display: none;
      }
    }

    &::before {
      content: '';
      display: block;
      height: 1px;
      width: 100%;
      top: 0;
      position: absolute;
      background: url(../img/dotted.png) center center no-repeat;
    }

    &__heading a {
      font-style: normal;
      font-weight: 500;
      font-size: 14px;
      line-height: 22px;
      color: #343A40;
    }

    .d-flex {
      margin-top: 12px;
      justify-content: space-between;
      align-items: center;

      .info {
        width: 200px;
      }

      .name {
        font-style: normal;
        font-weight: bold;
        font-size: 13px;
        line-height: 21px;
        color: #BD1723;
        text-transform: uppercase;
        @include Roboto();
      }

      .des {
        font-style: italic;
        font-weight: normal;
        font-size: 13px;
        line-height: 21px;
        color: #ADB5BD;
        @include Roboto();
      }

      .avatar {
        width: 35px;
        height: 35px;
        border-radius: 50%;
        overflow: hidden;
        background: #ADB5BD url(../img/img-user.png) center center/35px no-repeat;

        img {
          width: 100%;
        }
      }
    }
  }
}