@import "../../scss/bootstrap-4.1.2/scss/functions";
@import "../../scss/variables";
@import "../../scss/mixins/font-style";
@import "../../scss/mixins/story-image";
@import "../../scss/mixins/no-padding-margin-border";

.box-style-5 {
  .story {
    width: 297px;
    margin-top: 24px;

    &:first-child {
      margin-top: 0;
    }

    &__thumb {
      width: 297px;
      height: 170px;
      overflow: hidden;
    }

    &__heading a {
      font-style: normal;
      font-weight: 500;
      font-size: 16px;
      line-height: 26px;
      color: #343A40;
      padding-top: $spacer-1;
    }
  }

  .banner {
    margin-top: 24px;
    width: 297px;
  }
}