@import "../../scss/bootstrap-4.1.2/scss/functions";
@import "../../scss/variables";
@import "../../scss/mixins/font-style";
@import "../../scss/mixins/story-image";
@import "../../scss/mixins/no-padding-margin-border";

@mixin PlayfairDisplay {
  font-family: 'PlayfairDisplay';
}

@mixin Roboto {
  font-family: 'Roboto';
}

@mixin border-bottom {
  &:before {
    position: absolute;
    content: "";
    height: 5px;
    width: 45px;
    background: #bd1723;
    bottom: 0;
    left: 0;
    display: block;
  }
}

.box-style-6 {
  .box-heading {
    a {
      font-style: normal;
      font-weight: 900;
      font-size: 22px;
      line-height: 29px;
      text-transform: uppercase;
      color: #343A40;
      position: relative;
      padding-bottom: 13px;
      @include PlayfairDisplay();
      @include border-bottom();

    }
  }

  .box-content {
    margin-top: 28px;
    position: relative;

    .story {
      &__heading a {
        padding-top: $spacer-3;
        font-style: normal;
        font-weight: bold;
        font-size: 24px;
        line-height: 30px;
        color: #212529;
        @include PlayfairDisplay();
      }

      &__thumb {
        position: relative;

        span {
          background: rgba(0, 0, 0, 0.8);
          border-radius: 20px;
          padding: 10px 20px;
          position: absolute;
          color: $white;
          @include Roboto();
          font-style: normal;
          font-weight: normal;
          font-size: 15px;
          line-height: 20px;
          bottom: 23px;
          left: 16px;

          i {
            font-style: normal;
            font-weight: 900;
            font-size: 15px;
            line-height: 20px;
            color: #FFFFFF;
            margin-right: 14px;
          }
        }
      }
    }

    .prevArrow.slick-arrow {
      cursor: pointer;
      position: absolute;
      left: 0;
      top: 0;
      background: linear-gradient(90deg, rgba(0, 0, 0, 0.7) 0%, rgba(0, 0, 0, 0) 100%);
      width: 66px;
      height: 352.44px;
      z-index: 1;
    }

    .nextArrow.slick-arrow {
      cursor: pointer;
      position: absolute;
      background: linear-gradient(90deg, rgba(0, 0, 0, 0) 0%, rgba(0, 0, 0, 0.7) 100%);
      right: 0;
      top: 0;
      width: 66px;
      height: 352.44px;
    }

    .nextArrow i,
    .prevArrow i {
      font-style: normal;
      font-size: 30px;
      line-height: 20px;
      color: #FFFFFF;
      position: absolute;
      top: 50%;
      transform: translateY(-50%);
    }

    .prevArrow i {
      left: 8px;
    }

    .nextArrow i {
      right: 8px;
    }
  }
}