@import "../../scss/bootstrap-4.1.2/scss/functions";
@import "../../scss/variables";
@import "../../scss/mixins/font-style";
@import "../../scss/mixins/story-image";
@import "../../scss/mixins/no-padding-margin-border";

@mixin PlayfairDisplay {
  font-family: 'PlayfairDisplay';
}

@mixin border-bottom {
  &:before {
    position: absolute;
    content: "";
    height: 5px;
    width: 45px;
    background: #bd1723;
    bottom: 0;
    left: 0;
    display: block;
  }
}

.box-style-14 {
  .box-heading {
    a {
      font-style: normal;
      font-weight: 900;
      font-size: 22px;
      line-height: 29px;
      text-transform: uppercase;
      color: #343A40;
      @include PlayfairDisplay();
      position: relative;
      padding-bottom: 13px;
      display: block;

      @include border-bottom();
    }
  }

  .box-content {
    margin-top: 20px;

    .story {
      margin-top: 20px;

      &:first-child {
        margin-top: 0;
      }

      &__thumb {
        width: 297px;
        height: 178px;
        position: relative;

        span {
          position: absolute;
          left: 8px;
          bottom: 8px;
          width: 30px;
          height: 30px;
          border-radius: 50%;
          background: rgba(0, 0, 0, 0.7);
        }

        i {
          font-style: normal;
          font-weight: 900;
          font-size: 13px;
          line-height: 13px;
          color: #FFFFFF;
          right: 50%;
          top: 50%;
          position: absolute;
          transform: translate(50%, -50%);
        }
      }

      &__heading a {
        font-style: normal;
        font-weight: 500;
        font-size: 14px;
        line-height: 22px;
        color: #343A40;
        padding-top: 8px;
      }
    }
  }
}