@import "../../scss/bootstrap-4.1.2/scss/functions";
@import "../../scss/variables";
@import "../../scss/mixins/font-style";
@import "../../scss/mixins/story-image";
@import "../../scss/mixins/no-padding-margin-border";

@mixin PlayfairDisplay {
  font-family: 'PlayfairDisplay';
}


.box-style-19 {

  .slick-list {
    padding-left: 0px !important; // half partial
  }

  .slick-track {
    display: flex !important;
  }

  .slick-slide {
    height: inherit !important;
  }

  .prevArrow,
  .nextArrow {
    cursor: pointer;
    width: 82px;
    height: 108px;
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    padding: 168px 0;
  }

  .prevArrow {
    background: url(../img/control-right.png) center center no-repeat;
    right: -98px;
  }


  .nextArrow {
    left: -98px;
    background: url(../img/control-left.png) center center no-repeat;
  }

  .story {
    margin-right: 16px;
    text-align: center;
    padding: $spacer-3;
    background: #343A40;
    border-radius: 4px;
    position: relative;

    &__thumb {
      width: 231px;
      height: 154px;
    }

    &__heading a {
      font-style: normal;
      font-weight: 900;
      font-size: 17px;
      line-height: 20px;
      text-align: center;
      text-transform: uppercase;
      color: #FFFFFF;
      @include PlayfairDisplay();
      padding-top: $spacer-2;

      &:hover {
        color: #ccc !important;
      }
    }

    &__summary {
      font-style: normal;
      font-weight: normal;
      font-size: 14px;
      line-height: 20px;
      text-align: center;
      color: #FFFFFF;
      margin-top: $spacer-2;
      padding-bottom: 46px;
    }

    &__more {
      background: #fff;
      border-radius: 99px;
      font-style: normal;
      font-weight: 700;
      font-size: 12px;
      line-height: 18px;
      text-transform: uppercase;
      color: #000;
      padding: 6px 28px;
      display: block;
      margin: 0 auto;
      width: 125px;
      margin-top: 1.6rem;
      transition: .2s ease-in-out;
      position: absolute;
      bottom: 16px;
      right: 50%;
      transform: translateX(50%);

      &:hover {
        color: $primary;
      }
    }
  }
}