@mixin PlayfairDisplay {
  font-family: 'PlayfairDisplay';
}

.box-style-25 {
  background: #CFF4FC;
  border: 1px solid #9EEAF9;
  border-radius: 4px;
  margin-top: 32px !important;

  .heading {
    padding: 16px;
    font-style: normal;
    font-weight: bold;
    font-size: 14px;
    line-height: 19px;
    text-align: center;
    color: #055160;
    display: block;
    text-align: center;
    @include PlayfairDisplay();
  }
}