@import "../../scss/bootstrap-4.1.2/scss/functions";
@import "../../scss/variables";
@import "../../scss/mixins/font-style";
@import "../../scss/mixins/story-image";
@import "../../scss/mixins/no-padding-margin-border";

@mixin PlayfairDisplay {
  font-family: 'PlayfairDisplay';
}

@mixin border-bottom {
  &:before {
    position: absolute;
    content: "";
    height: 5px;
    width: 45px;
    background: #bd1723;
    bottom: 0;
    left: 0;
    display: block;
  }
}

.box-style-4 {
  margin-top: 38px;

  .box-heading {
    border-top: 8px solid #F8F9FA;

    a {
      padding-top: 20px;
      font-style: normal;
      font-weight: 900;
      font-size: 22px;
      line-height: 29px;
      text-transform: uppercase;
      color: #343A40;
      position: relative;
      display: block;
      padding-bottom: 13px;
      @include PlayfairDisplay();
      @include border-bottom();
    }
  }

  .box-content {
    margin-top: 20px;
    justify-content: space-between;

    .story {
      position: relative;

      &::after {
        position: absolute;
        bottom: 0;
        background: url(../img/mask.png) center center no-repeat;
        width: 297px;
        height: 275px;
        content: "";
      }

      &__thumb {
        width: 297px;
        height: 440px;
      }

      &__heading {
        position: absolute;
        bottom: 0;
        width: 100%;
        right: 50%;
        transform: translateX(50%);
        z-index: 1;

        a {
          @include PlayfairDisplay();
          font-style: normal;
          font-weight: bold;
          font-size: 20px;
          line-height: 28px;
          text-align: center;
          color: #FFFFFF;
          padding: 357px 33px 28px;
          position: relative;

          &:hover {
            color: #ccc !important;
          }

          &::before {
            position: absolute;
            content: "";
            width: 45px;
            height: 1px;
            background: $primary;
            margin-top: -12px;
            right: 50%;
            transform: translateX(50%);
          }
        }
      }
    }
  }
}