@import "../../scss/bootstrap-4.1.2/scss/functions";
@import "../../scss/variables";
@import "../../scss/mixins/font-style";
@import "../../scss/mixins/story-image";
@import "../../scss/mixins/no-padding-margin-border";

@mixin NotoSans {
    font-family: 'NotoSans';
}

@mixin Roboto {
    font-family: 'Roboto';
}

.podcast {
    justify-content: space-between;
    margin-top: 48px;

    .ads {
        img {
            width: 300px;
            height: 250px;
        }
    }

    .box-heading {
        display: flex;
        align-items: center;
        background: url(../img/bg-radio.png) right 30px center no-repeat;

        .heading {
            font-style: italic;
            font-weight: normal;
            font-size: 34px;
            line-height: 46px;
            text-transform: uppercase;
            color: #000000;
            padding-top: 10px;
            position: relative;
            @include NotoSans();
            margin-right: 37px;

            &::before {
                position: absolute;
                top: 0;
                width: 55px;
                height: 4px;
                content: '';
                background: $primary;
            }
        }

        .text-1 {
            left: 0px;
            top: 0px;
            padding: 6px 16px;
            padding: 6px 16px;
            text-transform: uppercase;
            font-style: normal;
            font-weight: normal;
            font-size: 12px;
            line-height: 20px;
            text-align: center;
            text-transform: uppercase;
            color: #212529;
            @include Roboto();
            margin-right: 16px;
            display: flex;
            background: #F8F9FA;
        }

        .text-2 {
            font-style: normal;
            font-weight: normal;
            font-size: 12px;
            line-height: 20px;
            text-align: center;
            text-transform: uppercase;
            color: #000000;
            @include Roboto();
            padding: 6px 16px;
            background: white;
        }
    }

    .box-content {
        margin-top: 13px;

        .slick-track {
            display: flex !important;
        }

        .slick-slide {
            height: inherit !important;
        }

        .story {
            position: relative;
            border-right: 1px solid;
            border-color: rgba(0, 0, 0, .07);

            &::after {
                position: absolute;
                content: '';
                width: 46px;
                height: 46px;
                display: block;
                top: 72px;
                right: 50%;
                transform: translateX(50%);
                background: url(../img/icon-podcast.png) center center no-repeat;
            }

            &__thumb {
                margin: 0 auto;
                width: 110px;
                height: 110px;
                border-radius: 50%;
                overflow: hidden;
                position: relative;

                &::before {
                    position: absolute;
                    width: 110px;
                    height: 110px;
                    border: 3px solid rgba(255, 255, 255, .7);
                    content: '';
                    top: 0;
                    border-radius: 50%;
                    left: 0;
                }
            }

            &__heading {
                font-style: normal;
                font-weight: 500;
                font-size: 14px;
                line-height: 22px;
                text-align: center;
                display: block;
                padding-top: 16px;
                color: #343A40;
                width: 140px;
                margin: 0 auto;
            }
        }
    }
}