.box-style-10 {
  .box-content {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
  }

  .story {
    position: relative;
    width: 318px;
    margin-top: 5px;
    overflow: hidden;

    &:nth-child(-n+4) {
      margin-top: 0;
    }

    &::after {
      position: absolute;
      background: url(../img/mask-ver2.png) center center no-repeat;
      bottom: 0;
      content: "";
      width: 318px;
      height: 171px;
    }

    &__thumb {
      width: 318px;
      height: 212px;
    }

    &__heading {
      z-index: 1;
      position: absolute;
      bottom: 0;

      a {
        font-style: normal;
        font-weight: 500;
        font-size: 14px;
        line-height: 22px;
        text-transform: uppercase;
        color: #FFFFFF;
        padding: 111px 16px 16px;

        &:hover {
          color: #ccc !important;
        }
      }
    }

  }
}