@import "../../scss/bootstrap-4.1.2/scss/functions";
@import "../../scss/variables";
@import "../../scss/mixins/font-style";
@import "../../scss/mixins/story-image";
@import "../../scss/mixins/no-padding-margin-border";

@mixin PlayfairDisplay {
  font-family: 'PlayfairDisplay';
}

@mixin Roboto {
  font-family: 'Roboto';
}

.box-style-9 {
  .box-content {
    display: flex;
    justify-content: space-between;
  }

  .rank-1 {
    width: 478px;
    padding-right: $spacer-3;
    border-right: 1px solid;
    border-color: rgba(0, 0, 0, 0.1);

    .story {
      &__thumb {
        width: 462px;
        height: 284px;
      }

      &__heading a {
        font-style: normal;
        font-weight: 600;
        font-size: 18px;
        line-height: 26px;
        color: #000000;
        @include PlayfairDisplay();
        padding-top: 12px;
      }

    }
  }

  .rank-2 {
    width: 560px;
    padding: 0 $spacer-3;
    border-right: 1px solid;
    border-color: rgba(0, 0, 0, 0.1);

    .story {
      overflow: auto;
      margin-top: $spacer-3;
      padding-top: $spacer-3;
      border-top: 1px solid;
      border-color: rgba(0, 0, 0, 0.1);

      &:first-child {
        padding-top: 0;
        margin-top: 0;
        border-top: none;

      }

      &__thumb {
        width: 231px;
        height: 145px;
        float: right;
        margin-left: 12px;
      }

      &__heading a {
        font-style: normal;
        font-weight: 600;
        font-size: 18px;
        line-height: 26px;
        color: #000000;
        @include PlayfairDisplay();
      }

      &__summary {
        margin-top: 12px;
        font-style: normal;
        font-weight: normal;
        font-size: 13px;
        line-height: 22px;
        color: #6C757D;
        @include Roboto;
        overflow: hidden;
        text-overflow: ellipsis;
        display: -webkit-box;
        -webkit-line-clamp: 3;
        line-clamp: 3;
        -webkit-box-orient: vertical;
      }
    }
  }

  .rank-3 {
    width: 247px;
    padding-left: $spacer-3;

    .story {
      margin-top: $spacer-3;
      border-top: 1px solid;
      border-color: rgba(0, 0, 0, 0.1);
      padding-top: $spacer-3;

      &:first-child {
        padding-top: 0;
        border-top: none;
        margin-top: 0;
      }

      &__heading a {
        font-style: normal;
        font-weight: 600;
        font-size: 18px;
        line-height: 26px;
        color: #000000;
        @include PlayfairDisplay()
      }

    }

  }
}