@mixin PlayfairDisplay {
  font-family: 'PlayfairDisplay';
}

@mixin border-bottom {
  &:before {
    position: absolute;
    content: "";
    height: 5px;
    width: 45px;
    background: #bd1723;
    bottom: 0;
    left: 0;
    display: block;
  }
}

.box-style-23 {
  margin-top: 60px;

  .box-heading {
    a {
      font-style: normal;
      font-weight: 900;
      font-size: 28px;
      line-height: 29px;
      text-transform: uppercase;
      color: #343A40;
      position: relative;
      display: block;
      padding-bottom: 13px;
      @include PlayfairDisplay();

      @include border-bottom();
    }
  }

  .box-content {
    margin-top: 24px;
    justify-content: space-between;
    flex-wrap: wrap;

    >* {
      &:nth-child(-n+4) {
        .story {
          margin-top: 0;
        }
      }
    }

    .story {
      margin-top: 32px;

      &__thumb {
        width: 297px;
        height: 167px;
      }

      &__heading a {
        font-style: normal;
        font-weight: 600;
        font-size: 18px;
        line-height: 26px;
        color: #000000;
        padding-top: 8px;
        display: block;
        @include PlayfairDisplay();
      }

    }
  }
}