@mixin NotoSans {
    font-family: 'NotoSans';
}

@mixin Roboto {
    font-family: 'Roboto';
}

.box-style-29 {
    .box-heading {
        display: flex;
        align-items: center;
        justify-content: space-between;
        @include NotoSans();
    }

    .heading {
        width: 250px;
        font-style: normal;
        font-weight: 600;
        font-size: 22px;
        line-height: 30px;
        text-transform: uppercase;
        color: #0071BE;
        padding: 30px 149px 24px 10px;
        display: block;
        background: url(../img/moinhat.png) center center no-repeat;
    }

    ul {
        display: flex;
        align-items: center;
    }

    li {
        margin-left: 20px;

        &:first-child {
            margin-left: 0;
        }

        a {
            font-style: normal;
            font-weight: 600;
            font-size: 18px;
            line-height: 25px;
            text-align: right;
            color: #343A40;

            &:hover,
            &.active {
                color: #0073bf;
            }
        }
    }

    .box-content {
        display: flex;
        justify-content: space-between;
    }

    .region-1 {
        width: 231px;

        .story {
            margin-top: 12px;
            padding-top: 12px;
            border-top: 1px solid #E9ECEF;

            &:first-child {
                padding-top: 0;
                margin-top: 0;
                border-top: none;
                padding-bottom: 4px;

                .story__heading a {
                    padding-top: 15px;
                    font-size: 18px;
                    line-height: 26px;
                }
            }

            &__thumb {
                width: 231px;
                height: 154px;
            }

            &__heading a {
                @include Roboto();
                font-style: normal;
                font-weight: 500;
                font-size: 16px;
                line-height: 24px;
                color: #212529;
            }
        }
    }

    .region-2 {
        width: 661px;
        @include Roboto();

        .story {
            &__thumb {
                width: 661px;
                height: 372px;
            }

            &__heading a {
                font-style: normal;
                font-weight: 600;
                font-size: 26px;
                line-height: 30px;
                padding-top: 15px;
                color: #212529;
            }

            &__summary {
                font-style: normal;
                font-weight: normal;
                font-size: 14px;
                line-height: 22px;
                color: #6C757D;
                margin-top: 15px;
            }
        }
    }

}