@mixin Roboto {
    font-family: 'Roboto';
}

@mixin NotoSans {
    font-family: 'NotoSans';
}

.box-style-32 {
    margin-top: 52px;

    .heading {
        font-style: normal;
        font-weight: 600;
        font-size: 22px;
        line-height: 30px;
        text-transform: uppercase;
        color: #212529;
        position: relative;
        @include NotoSans();
        padding-left: 14px;

        &::before {
            position: absolute;
            content: '';
            width: 137px;
            height: 14px;
            background: #fff3cd;
            left: 0;
            bottom: 0;
            z-index: -1;
        }
    }

    .box-content {
        margin-top: 30px;

        .story {
            overflow: auto;
            margin-top: 24px;

            &:first-child {
                margin-top: 0;
            }

            &__thumb {
                width: 70px;
                height: 70px;
                float: left;
                margin-right: 16px;
            }

            &__heading a {
                font-style: normal;
                font-weight: 500;
                font-size: 16px;
                line-height: 24px;
                color: #212529;
                @include Roboto();
            }
        }
    }
}