@import "../../scss/bootstrap-4.1.2/scss/functions";
@import "../../scss/variables";
@import "../../scss/mixins/font-style";
@import "../../scss/mixins/story-image";
@import "../../scss/mixins/no-padding-margin-border";

@mixin Roboto {
    font-family: 'Roboto';
}

.inteview-interaction {
    background: #FFF3CD;
    border: 1px solid #FFE69C;
    box-sizing: border-box;
    border-radius: 4px;
    padding: $spacer-4 $spacer-3;

    >.title {
        font-style: normal;
        font-weight: 600;
        font-size: 18px;
        line-height: 21px;
        text-transform: uppercase;
        color: #212529;
        @include Roboto();
        display: block;
        text-align: center;
    }

    .form-interaction {
        margin-top: 12px;
        margin-bottom: 9px;

        textarea,
        input {
            width: 100%;
            background: #FFFFFF;
            border: 1px solid #FFE69C;
            font-style: normal;
            font-weight: normal;
            font-size: 12px;
            line-height: 20px;
            color: #ADB5BD;
            @include Roboto();
            padding: 8px 11px;

        }

    }

    .d-flex {
        justify-content: end;
    }

    .btn-send {
        background: #FFE69C;
        border: 1px solid #FFDA6A;
        box-sizing: border-box;
        border-radius: 3px;
        padding: 8px 12px;
        font-style: normal;
        font-weight: 500;
        font-size: 14px;
        line-height: 16px;
        color: #000000;
        @include Roboto();
    }
}