@import "../../scss/bootstrap-4.1.2/scss/functions";
@import "../../scss/variables";
@import "../../scss/mixins/font-style";
@import "../../scss/mixins/story-image";
@import "../../scss/mixins/no-padding-margin-border";

@mixin border-bottom {
  &:before {
    position: absolute;
    content: "";
    height: 5px;
    width: 45px;
    background: #bd1723;
    bottom: 0;
    left: 0;
    display: block;
  }
}

@mixin PlayfairDisplay {
  font-family: 'PlayfairDisplay';
}

@mixin Roboto {
  font-family: 'Roboto';
}

.box-style-7 {
  .box-heading {
    a {
      font-style: normal;
      font-weight: 900;
      font-size: 22px;
      line-height: 29px;
      text-transform: uppercase;
      color: #343A40;
      position: relative;
      padding-bottom: 13px;
      @include PlayfairDisplay();
      @include border-bottom();
    }
  }

  .box-content {
    margin-top: 28px;
    position: relative;

    .rank-1 {
      .story {
        overflow: auto;

        &__thumb {
          width: 264px;
          height: 177px;
          float: left;
          margin-right: $spacer-2;
          position: relative;

          span {
            position: absolute;
            bottom: 10px;
            left: 10px;
            border-radius: 50%;
            width: 40px;
            height: 40px;
            overflow: hidden;

            i {
              font-weight: 900;
              font-size: 18px;
              line-height: 18px;
              position: absolute;
              top: 50%;
              right: 50%;
              transform: translate(50%, -50%);
              color: $white;
            }
          }
        }

        &__meta a {
          margin-top: $spacer-1;
          font-style: normal;
          font-weight: bold;
          font-size: 12px;
          line-height: 20px;
          text-transform: uppercase;
          color: #BD1723;
        }

        &__heading a {
          font-style: normal;
          font-weight: bold;
          font-size: 18px;
          line-height: 24px;
          color: #212529;
          margin-top: 4px;
          @include PlayfairDisplay();
        }

        &__summary {
          margin-top: $spacer-1;
          font-style: normal;
          font-weight: normal;
          font-size: 13px;
          line-height: 22px;
          color: #6C757D;
          @include Roboto();
        }
      }
    }

    .rank-2 {
      padding-top: $spacer-3;
      margin-top: $spacer-3;
      justify-content: space-between;
      border-top: 1px solid #E9ECEF;
      position: relative;

      &::before {
        content: '';
        right: 50%;
        width: 1px;
        height: 100%;
        background: $border-color;
        position: absolute;
      }

      >*:nth-child(-n+2) {
        .story {
          border-top: none;
          padding-top: 0;
          margin-top: 0;
        }
      }

      .story {
        overflow: auto;
        margin-top: $spacer-3;
        padding-top: $spacer-3;
        border-top: 1px solid #E9ECEF;
        width: 297px;

        &:nth-child(-n+2) {
          border-top: 0;
          padding-top: 0;
          margin-top: 0;
        }

        &__thumb {
          width: 80px;
          height: 60px;
          float: left;
          margin-right: $spacer-3;
          position: relative;

          span {
            position: absolute;
            bottom: 10px;
            left: 10px;
            border-radius: 50%;
            width: 20px;
            height: 20px;
            overflow: hidden;
            background-size: 12px;

            i {
              font-style: normal;
              font-weight: 900;
              font-size: 10px;
              line-height: 10px;
              color: #FFFFFF;
              top: 50%;
              right: 50%;
              transform: translate(50%, -50%);
              position: absolute;
            }
          }
        }

        &__heading a {
          font-style: normal;
          font-weight: 500;
          font-size: 14px;
          line-height: 22px;
          color: #343A40;
        }
      }
    }
  }
}