@mixin Roboto {
  font-family: 'Roboto';
}

@mixin PlayfairDisplay {
  font-family: 'PlayfairDisplay';
}

.box-style-27 {
  border: 1px solid #DEE2E6;
  box-sizing: border-box;
  border-radius: 8px;
  padding: 24px;
  background: url(../img/bg_01.png) center center/100% 100% no-repeat;

  .thumb_list {
    position: relative;
    width: fit-content;
  }

  .story {
    .thumb_list {
      display: flex;
      margin-top: 10px;
    }

    &__heading {
      font-style: normal;
      font-weight: 800;
      font-size: 21px;
      line-height: 32px;
      text-transform: uppercase;
      color: #212529;
      @include PlayfairDisplay()
    }

    &__summary {
      font-style: normal;
      font-weight: normal;
      font-size: 13px;
      line-height: 22px;
      @include Roboto();
      color: #6C757D;
      margin-top: 10px;
    }

    &__thumb {
      width: 80px;
      height: 80px;
      border-radius: 8px;
      margin-left: 8px;
      position: relative;
      overflow: hidden;

      &:first-child {
        margin-left: 0;
      }

    }
  }

  .mask {
    position: absolute;
    right: 0;
    width: 80px;
    height: 80px;
    border-radius: 8px;
    background: rgba(0, 0, 0, .5);
    text-align: center;
    align-items: center;
    display: flex;
    justify-content: center;

    a {
      font-style: normal;
      font-weight: bold;
      font-size: 25px;
      line-height: 29px;
      text-transform: uppercase;
      color: #FFFFFF;
      @include Roboto();
      padding: 26px 15px;
    }
  }
}