@import "../../scss/bootstrap-4.1.2/scss/functions";
@import "../../scss/variables";
@import "../../scss/mixins/font-style";
@import "../../scss/mixins/story-image";
@import "../../scss/mixins/no-padding-margin-border";

@mixin PlayfairDisplay {
  font-family: 'PlayfairDisplay';
}

@mixin border-bottom {
  &:before {
    position: absolute;
    content: "";
    height: 5px;
    width: 45px;
    background: #bd1723;
    bottom: 0;
    left: 0;
    display: block;
  }
}

.box-style-16 {
  .box-heading {
    a {
      font-style: normal;
      font-weight: 900;
      font-size: 18px;
      line-height: 24px;
      color: #343A40;
      @include PlayfairDisplay();
      padding-bottom: 13px;
      position: relative;
      @include border-bottom();
      display: block;
    }
  }

  .box-content {
    margin-top: $spacer-3;

    .story {
      padding-top: $spacer-3;
      margin-top: $spacer-3;
      border-top: 1px solid $border-color;

      &:first-child {
        margin-top: 0;
        padding-top: 0;
        border-top: none;
      }

      &__heading a {
        font-style: normal;
        font-weight: normal;
        font-size: 14px;
        line-height: 20px;
        color: #212529;
        padding-left: 41px;
        background: url(../img/icon-01.png) left top/25px no-repeat;
      }

    }
  }
}