@import "../../scss/bootstrap-4.1.2/scss/functions";
@import "../../scss/variables";
@import "../../scss/mixins/font-style";
@import "../../scss/mixins/story-image";
@import "../../scss/mixins/no-padding-margin-border";

@mixin Roboto {
  font-family: 'Roboto';
}

.box-style-24 {
  background: #D1E7DD;
  border: 1px solid #A3CFBB;
  box-sizing: border-box;
  border-radius: 4px;
  padding: 24px 16px;

  .heading {
    font-style: normal;
    font-weight: 600;
    font-size: 18px;
    line-height: 21px;
    text-transform: uppercase;
    color: #212529;
    @include Roboto();
    display: block;
    text-align: center;
  }

  .box-content {
    margin-top: 20px;
    height: 372px;
    overflow-y: scroll;
  }

  .item {
    margin-top: 20px;
    align-items: center;

    &:first-child {
      margin-top: 0;
    }

    .avatar {
      width: 46px;
      height: 46px;
      border: 1px solid #DEE2E6;
      border-radius: 50%;
      margin-right: 12px;
      padding: 23px;
      overflow: hidden;
      position: relative;

      img {
        width: 100%;
        position: absolute;
        top: 0;
        right: 0;
      }
    }

    .info {
      .name {
        font-style: normal;
        font-weight: bold;
        font-size: 14px;
        line-height: 20px;
        color: #343A40;
        @include Roboto();
      }

      .des {
        font-style: normal;
        font-weight: normal;
        font-size: 14px;
        line-height: 20px;
        color: #6C757D;
        @include Roboto();
      }
    }
  }

  // INFO :  style for scroll
  .scroll::-webkit-scrollbar-track {
    -webkit-box-shadow: inset 0 0 4px #D1E7DD;
    border-radius: 10px;
    background-color: #D1E7DD;
  }

  .scroll::-webkit-scrollbar {
    width: 4px;
    background: #ddd;
  }

  .scroll::-webkit-scrollbar-thumb {
    border-radius: 10px;
    background-color: #ddd;
  }
}