@import "../../scss/bootstrap-4.1.2/scss/functions";
@import "../../scss/variables";
@import "../../scss/mixins/font-style";
@import "../../scss/mixins/story-image";
@import "../../scss/mixins/no-padding-margin-border";

@mixin Roboto {
  font-family: 'Roboto';
}

@keyframes slideRight {
  from {
    transform: translateX(0);
    opacity: 0;
  }

  to {
    transform: translateX(0);
    opacity: 1;
  }
}

@keyframes slideTopToBottom {
  from {
    height: 0;
    opacity: 0;
  }

  to {
    height: 100%;
    opacity: 1;
  }
}


.region-top-header {
  background: $primary;
  height: 48px;
  display: flex;
  align-items: center;
  position: fixed;
  z-index: 99;
  top: 0;
  width: 100%;

  .humburger {
    span {
      width: 12px;
      height: 2px;
      background-color: $white;
      display: block;
      margin-top: 3px;

      &:first-child {
        margin-top: 0;
      }
    }
  }

  .wrap-menu {
    border: 1px solid rgba(255, 255, 255, 0.3);
    box-sizing: border-box;
    border-radius: 4px;
    padding: 8.5px 12px;
    display: flex;
    cursor: pointer;
    align-items: center;
    height: 32px;

  }

  .region-top-header:hover+nav {
    display: inline;
  }

  .text {
    color: $white;
    font-weight: 500;
    font-size: 13px;
    line-height: 15px;
    text-transform: uppercase;
    padding-left: 12px;
    @include Roboto();
  }

  .search-button {
    margin-left: 8px;
    border: 1px solid rgba(255, 255, 255, 0.3);
    box-sizing: border-box;
    border-radius: 4px;
    padding: 4px 12px;

    &:hover {
      background: #a6111c;
      border-color: #a6111c;
    }

    i {
      font-size: 16px;
      color: $white;
    }
  }

  .region-1 {
    position: absolute;
    display: flex;
    left: 12px;
    height: 32px;

    .wrap {
      height: 40px;
      z-index: 9;
      cursor: pointer;
    }
  }

  .region-2 {
    position: absolute;
    right: 65%;
    transform: translateX(50%);

    @media (min-width:1441px) {
      right: 50%;
    }

    ul {
      display: flex;
      animation-name: slideTopToBottom;
      animation-duration: 1s;

      li:first-child {
        a {
          padding-left: 0;

          &::before {
            display: none;
          }
        }
      }

      a {
        color: $white;
        font-weight: normal;
        font-size: 14px;
        line-height: 22px;
        padding-left: 13px;
        position: relative;
        padding-top: 5px;
        padding-bottom: 5px;
        @include Roboto();

        &:active,
        &:hover {
          text-decoration: underline;
        }

        &::before {
          position: absolute;
          width: 4px;
          height: 1px;
          background: $white;
          top: 50%;
          left: 4px;
          content: "";
        }
      }
    }

    .logo-nhandan {
      background: url(../img/logo-mobile.png) center center/100% no-repeat;
      width: 96px;
      height: 27px;
      display: block;
      padding: 24px;
      display: none;
      animation-name: slideTopToBottom;
      animation-duration: 1s;
      font-size: 0;
    }
  }

  .region-3 {
    position: absolute;
    right: 12px;


    >ul {
      display: flex;
      align-items: center;

      >li {
        transition: .2s;

        &:hover {
          background-color: #a6111c
        }
      }

      li:last-child {
        a {
          position: relative;
          padding: 16px 12px 16px 16px;

          &::before {
            position: absolute;
            content: '';
            height: 16px;
            background: rgba(255, 255, 255, 0.3);
            left: 0;
            display: block;
            top: 50%;
            transform: translateY(-50%);
            width: 1px;
          }
        }
      }

      .user {
        position: relative;

        a {
          text-transform: inherit;
        }

        img {
          width: 24px;
          height: 24px;
          border-radius: 50%;
          background: $white;
          margin-right: 10px;
          border: 1px solid rgba(255, 255, 255, 0.8);
        }

        i {
          padding-right: 0;
          padding-left: 12px;
          font-size: 13px;
          line-height: 16px;
          padding-top: 2px;
        }

        &:hover .wrap-user {
          display: block;
        }
      }

      .wrap-user {
        cursor: pointer;
        display: none;
        padding-top: 10px;
        left: -67px;
        position: absolute;
        z-index: 1;

        &::before {
          content: "";
          position: absolute;
          width: 10px;
          height: 10px;
          background: $white;
          transform: rotate(45deg);
          top: 6px;
          right: 50%;
          box-shadow: 0px 0px 4px rgba(0, 0, 0, 0.25);
        }
      }

      .info-user {
        background: #FFFFFF;
        box-shadow: 0px 0px 4px rgba(0, 0, 0, 0.25);
        border-radius: 4px;
        width: 200px;
        display: block;




        li {
          width: 100%;
        }

        i {
          font-size: 18px;
          line-height: 18px;
          text-transform: uppercase;
          color: #212529;
          margin-right: 12px;
          padding-left: 0;
          width: 24px;
          height: 18px;
          font-weight: normal;
        }

        a {
          font-style: normal;
          font-weight: normal;
          font-size: 14px;
          line-height: 16px;
          color: #212529;
          padding: 16px;
          display: flex;
          align-items: center;

          &.active,
          &:hover {
            background: #F8F9FA;
          }
        }
      }

      i {
        font-size: 16px;
        color: $white;
        padding-right: 12px;
        font-weight: normal;
      }

      .fas.fa-map-marker-alt {
        font-weight: bold;
      }

      a {
        height: 48px;
        overflow: hidden;
        font-weight: 500;
        font-size: 13px;
        line-height: 15px;
        text-transform: uppercase;
        color: $white;
        padding: 16px 12px;
        display: flex;
        align-items: center;
        @include Roboto();
      }
    }
  }
}

.region-logo {
  height: 174px;
  background: $white;
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 48px;

  .logo {
    background: url(../img/logo.png) center center no-repeat;
    width: 354px;
    height: 109px;
    font-size: 0;
    display: block;
  }
}

.search-form {
  position: relative;
  display: none;
  margin-left: 8px;
  border: 1px solid $primary;
  animation-name: slideRight;
  animation-duration: 1s;

  input {
    background: #fff;
    color: #333;
    width: 200px;
    height: 31px;
    padding: 0 35px 0 14px;
    border: 1px solid $primary;
    border: none;
    border-radius: 3px;
  }

  i {
    position: absolute;
    right: 0;
    top: 50%;
    transform: translateY(-50%);
    font-size: 16px;
    color: $primary;
    cursor: pointer;
    padding: 8px 12px;
  }
}