@import "../../scss/bootstrap-4.1.2/scss/functions";
@import "../../scss/variables";
@import "../../scss/mixins/font-style";
@import "../../scss/mixins/story-image";
@import "../../scss/mixins/no-padding-margin-border";

@mixin RobotoRe {
  font-family: 'Roboto';
}

.region-hot-news {

  >ul {
    padding-left: 7px;
    margin-bottom: 6px;
    display: flex;
    align-items: center;
    overflow-x: scroll;
    overflow-y: hidden;

    li {
      display: inline-block;
    }

    a {
      padding: 0 15px;
      height: 24px;
      position: relative;
      display: flex;
      align-items: center;
      white-space: nowrap;

      &::before {
        background: #FFFAEB;
        content: "";
        position: absolute;
        left: 0;
        width: 100%;
        height: 24px;
        z-index: -1;
        transform: skew(-23deg);
      }
    }

    .text {
      font-weight: 500;
      font-size: 12px;
      line-height: 16px;
      text-transform: uppercase;
      height: 24px;
      margin-left: 5px;
      color: #343A40;

      &:hover {
        color: $primary;
      }
    }

    .hot-news {
      font-weight: bold;
      font-size: 13px;
      line-height: 18px;
      text-transform: uppercase;
      color: $white;
      height: 28px;

      &::before {
        background: $primary;
        height: 28px;
      }
    }
  }


  .wrap-time {
    // border-top: thick double #CED4DA;
    margin-top: $spacer-1;
    padding-top: 12px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    position: relative;

    &::after {
      position: absolute;
      content: '';
      background: url(../img/border.png) center center no-repeat;
      display: block;
      width: 100%;
      height: 4px;
      top: 0;
    }

    time {
      font-style: normal;
      font-weight: normal;
      font-size: 13px;
      line-height: 22px;
      color: #6C757D;
      @include RobotoRe();

    }

    ul {
      display: flex;
      align-items: center;
      justify-content: space-between;
    }

    li:first-child {
      a:before {
        display: none;
      }
    }

    a {
      font-style: normal;
      font-weight: normal;
      font-size: 13px;
      line-height: 22px;
      padding: 5px 5px 5px 8px;
      position: relative;
      color: #6C757D;
      @include RobotoRe();

      &:hover {
        color: $primary;
      }

      &::before {
        position: absolute;
        content: "";
        width: 4px;
        height: 1px;
        background: #6C757D;
        left: 0;
        top: 50%
      }
    }
  }

  // INFO :  style for scroll
  .scroll::-webkit-scrollbar-track {
    -webkit-box-shadow: inset 0 0 4px #eee;
    border-radius: 10px;
    background-color: #eee;
  }

  .scroll::-webkit-scrollbar {
    height: 4px;
    background: #ddd;
  }

  .scroll::-webkit-scrollbar-thumb {
    border-radius: 10px;
    background-color: #ddd;
  }
}


.hot-news-catepage {
  display: flex;
  align-items: center;
  overflow-x: scroll;
  overflow-y: hidden;
  margin-left: 0 !important;
  position: relative;
  padding-left: 0 !important;

  &::before {
    position: absolute;
    content: '';
    top: 0;
    right: 0;
    width: 86px;
    height: 20px;
    z-index: 1;
    background: linear-gradient(90deg, rgba(255, 255, 255, 0) 0%, #FFFFFF 100%);
  }


  .text {
    white-space: nowrap;
    padding-left: 0;
    padding-right: 20px !important;
    margin-left: 0 !important;

    i {
      font-weight: 900;
      font-size: 12px;
      line-height: 15px;
      text-transform: uppercase;
      color: #FFC107;
      padding-right: 6px;
      padding-bottom: 4px;
    }

    &::before {
      display: none;
    }
  }
}