.story {

    figure {
        margin: 0;
    }

    &__thumb {
        // border-radius: 4px;
        overflow: hidden;
        background: url(../img/background.png) center center/100% no-repeat;

        a {
            display: flex;
            width: 100%;
            height: 100%;
            transition: transform .5s ease-out .1s;
            transition: 0.5s;
        }

        img {
            width: 100%;
            height: 100%;
            object-fit: cover;
        }

    }

    &__heading a {
        display: block;
        transition: 0.5s;

        &:hover {
            color: $primary !important;
        }
    }

    time {
        display: block;
    }

    &__meta {
        a {
            &:hover {
                color: $primary;
            }
        }
    }
}

.homepage-thoinay,
.section-cate,
.section-article,
.longform-page,
.interview-page,
.live-thoinay {
    .story {
        &__heading a {
            &:hover {
                color: #0073bf !important;
            }
        }
    }
}