@import "../../scss/bootstrap-4.1.2/scss/functions";
@import "../../scss/variables";
@import "../../scss/mixins/font-style";
@import "../../scss/mixins/story-image";
@import "../../scss/mixins/no-padding-margin-border";

@mixin Roboto {
    font-family: 'Roboto';
}

.box-comment {
    margin-top: 32px !important;
    @include Roboto();

    .heading {
        font-style: normal;
        font-weight: 600;
        font-size: 30px;
        line-height: 35px;
        text-transform: uppercase;
        color: #212529;
        display: block;
        pointer-events: none;
        @include Roboto();
    }

    textarea {
        background: $white;
        border: 1px solid #CCCCCC;
        box-sizing: border-box;
        border-radius: 3px;
        display: block;
        width: 100%;
        padding: .375rem .75rem;
        font-size: 1.6rem;
        line-height: 1.4;
        color: #495057;
    }

    .box-content {
        margin-top: 24px !important;

        .wrap-comment {
            display: flex;

            .wrap {
                margin-left: $spacer-3;
                width: 100%;
            }
        }
    }

    .wrap-login {
        display: flex;
        align-items: center;

        span {
            font-style: normal;
            font-weight: normal;
            font-size: 14px;
            line-height: 20px;
            color: #6C757D;
            padding-right: 15px;
        }

        button {
            width: 26px;
            height: 26px;
            margin-right: 7px;
            border-radius: 50%;

            i {
                font-size: 13px;
                line-height: 15px;
                color: #FFFFFF
            }

            &:last-of-type {
                margin-right: 15px;
            }
        }

        .fb,
        .mail {
            background: #3b5998;
            display: flex;
            align-items: center;
            justify-content: center;
        }

        .zalo {
            background: #03A5FA url(../img/icon-zalo-02.png) center center no-repeat;
        }

        .google {
            background: #4285F4;
            display: flex;
            align-items: center;
            justify-content: center;
        }

        .login {
            font-style: normal;
            font-weight: normal;
            font-size: 14px;
            line-height: 20px;
            color: #032B82;
        }
    }

    .total-comment {
        font-weight: bold;
        font-size: 13px;
        line-height: 15px;
        text-transform: uppercase;
        color: var(--color-heading);
    }

    .wrap-select {
        label {
            font-weight: normal;
            font-size: 13px;
            line-height: 15px;
            text-transform: uppercase;
            // color: #333333;
            color: var(--color-heading);
            margin-bottom: 0;
        }

        select {
            background: #FFFFFF url(../img/vector.png) 172px 15px no-repeat;
            border: 1px solid #E5E5E5;
            box-sizing: border-box;
            outline: none;
            padding: 10px;
            font-weight: normal;
            font-size: 13px;
            line-height: 15px;
            color: #555555;
            cursor: pointer;
            width: 200px;
            appearance: none;
            -moz-appearance: none;
            -webkit-appearance: none;
            position: relative;
        }
    }

    .topic-comment {
        display: flex;
    }

    .sub-comment {
        margin-left: 50px;
        margin-top: $spacer-3;
        border-top: 1px solid #ddd;
        padding-top: $spacer-3;

        .wrap-comment {
            margin-top: 15px;

        }



        .avatar {
            width: 30px;
            height: 30px;
            background: #e5e5e5 url(../img/user.png) center center/15px no-repeat;
        }

    }

    .w-100.mt-3 {
        justify-content: flex-end;
    }

    .btn-Submit {
        width: 114px;
        height: 28px;
        left: 645px;
        top: 68px;
        background: #CE2E0E;
        border-radius: 3px;
        color: #FFFFFF;
        font-size: 14px;
        line-height: 20px;
        text-transform: none;
    }

    .sub-comments {
        .comment {
            display: flex;
        }
    }

    .avatar {
        width: 50px;
        height: 50px;
        border-radius: 50%;
        padding: 15px;
        background: #CED4DA url(../img/user.png) center no-repeat;
        position: relative;
        overflow: hidden;

        img {
            position: absolute;
            top: 0;
            right: 0;
            width: 100%;
        }
    }

    .user {
        margin-top: 20px;

        .comment.inner-comment {
            border-top: 1px solid #ddd;
            padding-top: $spacer-3;
            margin-top: $spacer-3;

            &:first-child {
                border-top: 0;
                padding-top: 0;
                margin-top: 0;
            }
        }

        .content-comment {
            margin-left: 16px;
            width: 100%;

            .info-author {
                font-weight: 500;
                font-size: 18px;
                line-height: 22px;
                color: #212529;
                font-style: normal;
            }

            .text {
                font-style: normal;
                font-weight: normal;
                font-size: 14px;
                line-height: 20px;
                color: #212529;
                margin-top: 8px;
            }
        }

        .action-comment {
            margin-top: 8px;

            >* {
                margin-right: 45px;
                font-style: normal;
                font-weight: 500;
                font-size: 14px;
                line-height: 20px;
                color: #6C757D;

                &:last-child {
                    margin-right: 0;
                }
            }

            .btnLike {
                &.active {
                    color: $primary;
                }
            }

            i {
                padding-right: 8px;
                font-size: 16px;
                line-height: 20px;
                color: #555555;
            }

        }

        .action-comment-reply {
            margin-top: 15px;

            textarea {
                background: $white;
                border: 1px solid #CCCCCC;
                box-sizing: border-box;
                border-radius: 3px;
                display: block;
                width: 100%;
                padding: .375rem .75rem;
                font-size: 1.6rem;
                line-height: 1.4;
                color: #495057;
            }
        }


    }

    .btn.btn-link {
        width: 228px;
        height: 46px;
        left: 298.5px;
        top: 0;
        background: #e21f26;
        border-radius: 3px;
        font-style: normal;
        font-weight: 500;
        font-size: 16px;
        line-height: 19px;
        text-transform: uppercase;
        color: #fff;
        margin: 0 auto;
        display: flex;
        align-items: center;
        justify-content: center;


        &::after {
            display: inline-block;
            width: 0;
            height: 0;
            margin-left: .2125em;
            vertical-align: .2125em;
            content: "";
            border-top: .25em solid;
            border-right: .25em solid transparent;
            border-bottom: 0;
            border-left: .25em solid transparent;
        }
    }

    // .btn-Submit,
    .btnSubmit {
        width: 149px;
        height: 35px;
        left: 676px;
        top: 0px;
        background: #CE2E0E;
        border-radius: 3px;
        color: #FFFFFF;
        font-style: normal;
        font-weight: 500;
        font-size: 16px;
        line-height: 19px;
        text-transform: uppercase;

    }

    .btnSubmit {
        margin-top: 1.6rem;
        float: right;
    }
}