@mixin NotoSans {
    font-family: 'NotoSans';
}

@mixin Roboto {
    font-family: 'Roboto';
}

.box-style-31 {
    margin-top: 52px;

    .heading {
        font-style: normal;
        font-weight: 600;
        font-size: 22px;
        line-height: 30px;
        /* identical to boxheight */
        text-transform: uppercase;
        color: #212529;
        position: relative;
        @include NotoSans();
        padding-left: 14px;

        &::before {
            position: absolute;
            content: '';
            width: 137px;
            height: 14px;
            background: #fff3cd;
            left: 0;
            bottom: 0;
            z-index: -1;
        }
    }

    .box-content {
        margin-top: 22px;

        .story {
            margin-top: 24px;

            &:first-child {
                margin-top: 0;
            }

            &__thumb {
                width: 330px;
                height: 220px;
                box-shadow: 4px 4px #CED4DA, 8px 8px #E9ECEF;
                margin-bottom: 8px;
            }

            &__heading a {
                font-style: normal;
                font-weight: 500;
                font-size: 18px;
                line-height: 26px;
                color: #212529;
                padding-top: 8px;
                @include Roboto();
            }
        }
    }
}