@mixin PlayfairDisplay {
  font-family: 'PlayfairDisplay';
}

@mixin PlayfairDisplayBoldItalic {
  font-family: 'PlayfairDisplay';
  src: url('../fonts/PlayfairDisplay/PlayfairDisplay-BoldItalic.ttf');
  font-weight: 700;
}

.box-style-11 {
  overflow: hidden;

  .box-heading {
    .wrap-heading {
      position: relative;
      font-style: normal;
      font-weight: 900;
      font-size: 20px;
      line-height: 26px;
      color: #BD1723;
      @include PlayfairDisplay();
      background: #fff;
      margin-left: 20px;
      padding: 0 10px;
      text-transform: uppercase;
      width: fit-content;

      &::before,
      &:after {
        position: absolute;
        height: 11px;
        background: #E6EEF6;
        content: "";
        width: 50%;
        top: 50%;
        transform: translateY(-50%);
        z-index: -1;

      }

      &::before {
        left: -30px;
      }

      a {
        font-style: normal;
        font-weight: 900;
        font-size: 20px;
        line-height: 26px;
        color: #BD1723;
        @include PlayfairDisplay();
      }
    }
  }

  .box-content {
    margin-top: 6px;

    .story {
      position: relative;
      min-height: 96px;

      &:nth-child(1) {
        background: url(../img/1.png) right 13px no-repeat;
      }

      &:nth-child(2) {
        background: url(../img/2.png) right 13px no-repeat;
      }

      &:nth-child(3) {
        background: url(../img/3.png) right 13px no-repeat;
      }

      &:nth-child(4) {
        background: url(../img/4.png) right 13px no-repeat;
      }

      &:nth-child(5) {
        background: url(../img/5.png) right 13px no-repeat;
      }

      &__thumb {
        width: 54px;
        height: 50px;
      }

      &__heading {

        a {
          font-style: normal;
          font-weight: 500;
          font-size: 14px;
          line-height: 22px;
          color: #343A40;
          padding-top: 14px;
        }
      }
    }
  }
}